import {
    HttpRequest,
    HttpHandler,
    HttpEvent,
    HttpInterceptor,
    HttpResponse,
    HttpErrorResponse
} from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { Injectable } from '@angular/core';
import { InMemoryStorageProvider } from '../../providers/in-memory-storage/in-memory-storage';
import { LOCAL_STORAGE, API_HOST, APPLICATION_TYPE } from '../constants';
import { LocalStorageProvider } from '../../providers/local-storage/local-storage';
import { LoginServiceProvider } from '../../providers/login-service/login-service';
import { BehaviorSubject } from 'rxjs';
import { Events, AlertController } from 'ionic-angular';
import { TranslateService } from '@ngx-translate/core';


@Injectable()
export class CustomInterceptor implements HttpInterceptor{
    isRefreshingToken: boolean = false;
    applicationType = APPLICATION_TYPE;
    tokenSubject: BehaviorSubject<string> = new BehaviorSubject<string>(null);
    constructor(public inMemoryStorage:InMemoryStorageProvider, 
                public localStorage: LocalStorageProvider,
                public loginService:LoginServiceProvider,
                private event: Events,
                public alertCtrl: AlertController,
                public translate: TranslateService
                ){

    }

    intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        let setHeaderToRequest = this.filterRequestToSetToken(request);
        if (!setHeaderToRequest) {
            let token = this.inMemoryStorage.get(LOCAL_STORAGE.TOKEN) || this.inMemoryStorage.get(LOCAL_STORAGE.WEBTOKEN);       
                    let requestclone;
                    if(token){
                        const headers = request.headers.set('Authorization', 'Bearer ' + token['access_token']).append('Content-Type', 'application/json');
                        requestclone = request.clone({
                            headers
                        });
                    } else {
                        requestclone = request;
                    }
                    return next.handle(requestclone).catch((error, caught) => {
                        if (error instanceof HttpErrorResponse) {
                            if (error.status === 401) {
                                console.info("Token is invalid 401 creating new token");
                                return this.handle401Code(request,next,token);
                            }else if(error.status == 400){
                               //this.logoutUser();
                               return Observable.throw(error);
                            } else if(error.status == 500){
                               return Observable.throw(error);
                            }  else if(error.status==0){
                                //this.commonUtils.showErrorToast('Internet connection not available.');
                                return Observable.throw(error);
                            }
                            else{
                                return Observable.throw(error);
                             }
                        }
                        else if (error.status==0){
                            alert('Internet connection is not available');
                            return Observable.throw(error);
                        }
                        else {
                            return Observable.throw(error);
                        }
                    })
        }
        return next.handle(request).map(response => {
            if (response instanceof HttpResponse) {
                if(response.status==0){
                    console.info('Internet connection not available');
                    return []; 
                } else if(response.status == 400){
                    return this.logoutUser();
                }
            }
            return response;
        })
    }
    filterRequestToSetToken(request) {

        
        let requests: string[] = [
            API_HOST + '/api/common/authenticate',
            API_HOST + '/oauth/token',
            './assets/i18n/en.json',
            API_HOST + '/api/user/password',
        ]
        if(request.url.endsWith("assets/Gujarat-Topo.json") || request.url.endsWith("assets/Gujarat-State-Topo.json")){
            return true; 
        }
        return requests.some((element) => {
            return element == request.url;
        });
    }
    addToken(req: HttpRequest<any>, token: any): HttpRequest<any> {
        return req.clone({ setHeaders: { Authorization: 'Bearer ' + token.access_token }})
    }

    handle401Code(req: HttpRequest<any>, next: HttpHandler, token) {
        if (!this.isRefreshingToken) {
            this.isRefreshingToken = true;
             console.info('Refreshing token:'+token);
            // Reset here so that the following requests wait until the token
            // comes back from the refreshToken call.
            this.tokenSubject.next(null);
            let token1 = this.inMemoryStorage.get(LOCAL_STORAGE.TOKEN) || this.inMemoryStorage.get(LOCAL_STORAGE.WEBTOKEN);
                return this.loginService.refreshTokenService(token1)
                .switchMap((newToken: string) => {
                    if (newToken) {
                        if(this.inMemoryStorage.get(LOCAL_STORAGE.TOKEN)){
                            this.inMemoryStorage.put(LOCAL_STORAGE.TOKEN,newToken);
                            this.localStorage.set(LOCAL_STORAGE.TOKEN, newToken).then(()=>{
                                console.info("Token updated to local storage");
                            })
                        }else if(this.inMemoryStorage.get(LOCAL_STORAGE.WEBTOKEN)){
                            this.inMemoryStorage.put(LOCAL_STORAGE.WEBTOKEN,newToken);
                            this.localStorage.set(LOCAL_STORAGE.WEBTOKEN, newToken).then(()=>{
                                console.info("Token updated to local storage");
                            })
                        }
                        this.isRefreshingToken = false;
                        this.tokenSubject.next(newToken);
                        return next.handle(this.addToken(req, newToken));
                    }

                    // If we don't get a new token, we are in trouble so logout.
                    return this.logoutUser();    
                })
                .catch(error => {
                    this.isRefreshingToken = false;
                    // If there is an exception calling 'refreshToken', bad news so logout.
                    if(this.applicationType === 'MOBILE'){
                        this.logoutUser();
                    }else {
                        this.logoutUser();
                        let alert = this.alertCtrl.create({
                            title: this.translate.instant('UnauthorizedAccess'),
                            message: this.translate.instant('ContactAdmin')
                          });
                          alert.present();
                          return;
                    }
                    return Observable.throw(error);     // return exception, because must return promise.
                })
                .finally(() => {
                    
                });
        } else {
            console.info('Updating token so request in waiting.');
            return this.tokenSubject
                .filter(token => token != null)
                .take(1)
                .switchMap(token => {
                    return next.handle(this.addToken(req, token));
                });
        }
    }    
     logoutUser():any{
        let user = this.inMemoryStorage.get(LOCAL_STORAGE.LOGIN_USER);
        this.loginService.logout(user);
        this.event.publish('openLoginPage');

        return;
    }
}


