import { Component } from '@angular/core';
import { QueryExecutorProvider } from '../../providers/query-executor/query-executor';
import { NavParams, LoadingController } from 'ionic-angular';
import { CallNumber } from '@ionic-native/call-number';
import { UtilityHelperProvider } from '../../providers/utility-helper/utility-helper';
import { StateOfHealthServiceProvider } from '../../providers/state-of-health-service/state-of-health-service';
import { LIMIT } from '../../app/constants';

/**
 * Generated class for the MemberLineListComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'member-health-infra-line-list',
  templateUrl: 'member-health-infra-line-list.html'
})
export class MemberHealthInfraLineListComponent {

  lineList: any = [];
  elementName: any;
  locationId: any;
  element: any;
  selectedDateFilter:any
  elementConfig:any;
  fieldConfig:any;
  keys:any =[];
  type:any;
  lineListLabel:any;
  offset:number = 0;
  showMoreButton: boolean = true;
  queryCode: any;
  healthInfraId: any;
  title : any;
  name: any;
  searchTxt:string= null;
  constructor(private queryExecutor: QueryExecutorProvider,
    private loading: LoadingController,
    private navParam: NavParams,
    private utilHelper : UtilityHelperProvider) {
    this.elementName = this.navParam.get('elementName');
    this.locationId = this.navParam.get('locationId');
    this.element = this.navParam.get('element');
    this.selectedDateFilter = this.navParam.get('selectedDateFilter');
    this.elementConfig = this.navParam.get('selectedElementConfig');
    this.fieldConfig = this.navParam.get('fieldConfig');
    this.type = this.navParam.get('type');
    this.queryCode = this.navParam.get('queryCode')
    this.healthInfraId = this.navParam.get('healthInfraId')
    this.title = this.navParam.get('title');
    this.name = this.navParam.get('name');
    this.init();
  }
  onShowMore(member){
    if(member.isShowMore==undefined){
      member.isShowMore= true;
    }else{
      member.isShowMore = !member.isShowMore;
    }
  }
  init() {
    let params = {
      "code": this.queryCode,
      "parameters": { "location_id": null, "health_infra_id": this.healthInfraId, "limit" : LIMIT,"offset":this.offset,search_text:this.searchTxt==''? null : this.searchTxt }
    };
    let making = this.loading.create();
    making.present();
    this.queryExecutor.executeQuery(params).subscribe(response => {
      console.log(response);
      if(response.result.length < LIMIT){
        this.showMoreButton = false;
      }
      this.lineList = this.lineList.concat(response.result);
      if(this.lineList.length>0){
      this.keys = Object.keys(this.lineList[0]);
      }
      this.offset += LIMIT
      making.dismiss();
    }, (err) => {
      making.dismiss();
      this.utilHelper.onApiFailure(err);
    })
  }
  onSearch(event){
    this.onResetParams();
    this.init();
  }
  onResetParams(){
    this.offset=0;
    this.lineList=[];
    this.showMoreButton=true;
  }
  onClearTest(){
    this.onResetParams();
    this.searchTxt=null;
    this.init();
  }
}



