import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { API_HOST, LOCAL_STORAGE } from '../../app/constants';
import { InMemoryStorageProvider } from '../in-memory-storage/in-memory-storage';
import { LocalStorageProvider } from '../local-storage/local-storage';
import { LoadingController } from 'ionic-angular';

/*
  Generated class for the LoginServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class LoginServiceProvider {

  constructor(public http: HttpClient, 
              public inMemoryStorageProvier:InMemoryStorageProvider,
              public localStorage:LocalStorageProvider,
              private loading: LoadingController) {
    
  }
  public loginWithLoginCode(loginCode){
    return this.login(null,null,loginCode);
  }
  public login(userName,password,loginCode=null):Observable<any>{
    let headers = new HttpHeaders({
      'Authorization': "Basic c29oLWFwcDpzb2gtYXBwLXNlY3JldA==",
      'Content-Type':'application/x-www-form-urlencoded',
      'Origin':'techo.gujarat.gov.in'
    });
    let postParam = new URLSearchParams();
    postParam.set('grant_type',"password");
    if(loginCode){
      postParam.set("login_code",loginCode);
    }
    postParam.set("username",userName);
    postParam.set("password",password);
    postParam.set("loginas",'');
    postParam.set("client_id",'soh-app');

    let formData = new FormData();
    formData.append('userName','username')
    return this.http.post<any>(API_HOST+'/oauth/token', postParam.toString(), {headers : headers});
  }
  public getLoginUser():Observable<any>{
    return this.http.get(API_HOST+'/api/login/principle');
  }  
  public refreshTokenService(tokenKey): Observable<any>{
    let postParam = new URLSearchParams();
    if(tokenKey){
    let refresh_token = tokenKey.refresh_token;
    postParam.set('grant_type',"refresh_token");
    postParam.set("refresh_token",refresh_token);
    var headersForm = new HttpHeaders({
      'Authorization': "Basic c29oLWFwcDpzb2gtYXBwLXNlY3JldA==",
      'Content-Type':'application/x-www-form-urlencoded'
    });
      let url =  API_HOST+'/oauth/token';
      return this.http.post<any>(url,postParam.toString(),{withCredentials: true, headers: headersForm})
    }else{
      return Observable.empty<Response>();
    }
  }
  public refreshToken() : Promise<any>{
    let token =  this.inMemoryStorageProvier.get(LOCAL_STORAGE.TOKEN)
    return this.refreshTokenService(token).toPromise();
  }
  public logout(user){
    this.dismissAllLoading();
    if(user && user.userName){
      let url = API_HOST + '/api/user/logout'
      this.http.get(url, { params: { "userName" : user.userName , "clientId" : "soh-app"} }).subscribe();
    }
    this.localStorage.remove(LOCAL_STORAGE.TOKEN).then(()=>{
        console.info("TOKEN Removed from local storage");
    })
    this.localStorage.remove(LOCAL_STORAGE.LOGIN_USER).then(()=>{
      console.info("LOGIN_USER Removed from local storage");
    })
    this.localStorage.remove(LOCAL_STORAGE.IS_ANONYMOUS_LOGIN).then(()=>{
      console.info("IS_ANONYMOUS_LOGIN Removed from local storage");
    })

    this.localStorage.remove(LOCAL_STORAGE.LOGIN_CODE).then(()=>{
      console.info("LOGIN_CODE Removed from local storage");
    })
  }
  public dismissAllLoading(){
    setTimeout(() => {
      let loading = this.loading.create();
      loading.present().then(data=>{
      try{
          console.info('dismissAll loading');
          loading.dismissAll();
      }catch(ignore){
           console.warn(ignore);
      }
      });
    }, 1000);
  }
}



