import { Observable } from "rxjs";
import { LOCAL_STORAGE } from "./constants";
import { InMemoryStorageProvider } from "../providers/in-memory-storage/in-memory-storage";
import { Injectable } from "@angular/core";
import { TranslateLoader } from "@ngx-translate/core";

@Injectable()
export class TranslateLSLoader implements TranslateLoader {

    public getTranslation(labels : Object): Observable<any> {
        return Observable.create(observer => {
            if(labels) {
                observer.next(labels);
            }
        });
    }
} 


