import { Component, OnDestroy, ViewChild } from '@angular/core';
import { NavController, LoadingController, NavParams, Events, Platform,Slides } from 'ionic-angular';
import { StateOfHealthDetailsComponent } from '../state-of-health-details/state-of-health-details';
import { LOCATION_LEVEL, LOCAL_STORAGE, TIME_LINE, ROOT_LOCATION} from '../../app/constants';
import { QueryExecutorProvider } from '../../providers/query-executor/query-executor';
import { UtilityHelperProvider } from '../../providers/utility-helper/utility-helper';
import { InMemoryStorageProvider } from '../../providers/in-memory-storage/in-memory-storage';
import { StateOfHealthServiceProvider } from '../../providers/state-of-health-service/state-of-health-service';

import { StatusReportComponent } from '../status-report/status-report';
import { Covid19ResultsComponent } from '../covid19-results/covid19-results';
import { LocalStorageProvider } from '../../providers/local-storage/local-storage';
/**
 * Generated class for the StateOfHealthComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'state-of-health',
  templateUrl: 'state-of-health.html'
})
export class StateOfHealthComponent {
  @ViewChild(Slides) slides: Slides;
  
  isDataLoad: boolean = false;
  isDataAvailable: boolean = false;
  isServerDown: boolean = false;
  drillingEnable: boolean = true;
  data: any;
  result:any;
  dateFilter:any;
  selectedDateFilter:any;
  dateFilterOptions:any;
  locationId:any;
  elementsConfig:any;
  showFilter: boolean = true;
  subscription: any;
  timeLineLabel:any='{'+TIME_LINE+'}';
  constructor(private navCtrl: NavController,
    public events2: Events,
    private queryExecutor: QueryExecutorProvider,
    private inMemoryStorage: InMemoryStorageProvider,
    private loading: LoadingController,
    public localStorage: LocalStorageProvider,
    private stateOfHealthService:StateOfHealthServiceProvider,
    private utilHelper:UtilityHelperProvider,
    private params: NavParams,
    ) {

    this.locationId = ROOT_LOCATION.LOCATION_ID;
    if (params.get('drillingEnable') === false) {
      this.drillingEnable = params.get('drillingEnable');
    }
    if (params.get('locationId')) {
      this.locationId = params.get('locationId');
    } else {
      let loginUser = this.inMemoryStorage.get(LOCAL_STORAGE.LOGIN_USER);
      if (loginUser) {
        this.locationId = loginUser.minLocationId;
      }
    }
    this.elementsConfig = this.stateOfHealthService.getElementConfig();
    let element = this.elementsConfig[0].elements.find(element => !element.isHidden && element.isFilterEnable === true )
    if(!element){
        this.showFilter = false;
    }
    this.init(this.locationId);
  }
  slideChanged() {
    let currentIndex = this.slides.getActiveIndex();
    let element = this.elementsConfig[currentIndex] && this.elementsConfig[currentIndex].elements.find(element => !element.isHidden && element.isFilterEnable === true )
    if (!element) {
      this.showFilter = false;
    } else {
      this.showFilter = true;
    }
  }
  init(locationId) {
    this.selectedDateFilter = this.inMemoryStorage.get(LOCAL_STORAGE.CORRENT_DATE_FILTER)
    this.dateFilter = this.selectedDateFilter.value;
    this.dateFilterOptions = this.inMemoryStorage.get(LOCAL_STORAGE.TIME_LINE_INTERVALS);
    this.localStorage.get(LOCAL_STORAGE.LOGIN_USER).then(user => {
      let userId;
      if (user) {
        userId = user.id;
      }
      let making = this.loading.create();
      making.present();
      this.queryExecutor.execute({ "code": "get_elements_details_permission_wise_3", "parameters": { "locationId": locationId || ROOT_LOCATION.LOCATION_ID, 'timeline_type': this.selectedDateFilter.value, 'user_id': userId } }
      ).subscribe((data) => {
        this.result = data.result;
        this.isDataLoad = true;
        this.data = this.stateOfHealthService.prepareData(this.result);
        this.isDataAvailable = Object.keys(this.data).length > 0;
        this.isServerDown = false;
        making.dismiss();
      }, (error) => {
        this.isDataLoad = true;
        if (error.status == 0) {
          this.isServerDown = true;
        } else {
          this.isDataAvailable = false;
        }
        making.dismiss();
      })
    })
  }
  
  public onShowDetails(element,selectedElementConfig) {
    let locationLevel = LOCATION_LEVEL.STATE;
    if (element.level == 2 || element.level == 3) {
      locationLevel = LOCATION_LEVEL.DISTRICT;
    }
    if(element.elementName === "COVID_STATUS_REPORT"){
      this.navCtrl.push(StatusReportComponent)
      return;
    } else if(element.elementName === "COVID19_HEALTH_INFRA_REPORT"){
      this.navCtrl.push(Covid19ResultsComponent);
      return;
    }
    if (this.navCtrl.parent) {    // for tab view
      this.navCtrl.parent.parent.push(StateOfHealthDetailsComponent, { element: element, elements: this.data, locationLevel: locationLevel, drillingEnable: this.drillingEnable, dateFilter:this.selectedDateFilter, selectedElementConfig:selectedElementConfig,tabName:selectedElementConfig.tabs[0].tabName});
    } else {
      this.navCtrl.push(StateOfHealthDetailsComponent, { element: element, elements: this.data, locationLevel: locationLevel, drillingEnable: this.drillingEnable, dateFilter: this.selectedDateFilter, selectedElementConfig:selectedElementConfig,tabName:selectedElementConfig.tabs[0].tabName});
    }
  }
  public onDateFilterChange(newvalue){
    this.selectedDateFilter = this.dateFilterOptions.find(interval => interval.value === newvalue);
    this.inMemoryStorage.put(LOCAL_STORAGE.CORRENT_DATE_FILTER,this.selectedDateFilter);
    this.data = this.stateOfHealthService.prepareData(this.result);
    this.init(this.locationId);
  }
  public isLogin(){
    return this.utilHelper.isLogin();
  }
  public onMenuClick(){
    let alert = this.utilHelper.getFilterMenu(this.selectedDateFilter);
    alert.addButton({
      text: 'OK',
      handler: filter => {
        this.onDateFilterChange(filter);      
      }
    });
    alert.present();
  }
  public getFile(elementConfig){
    return this.stateOfHealthService.getFile(elementConfig);  
  }
}



