import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { LOCATION_LEVEL, NAME_MAPPING_MAP, STAT_DISTRICT, CORPORATION, DEFAULT_MAP_COLOR, APPLICATION_TYPE } from '../../app/constants';
import * as d3 from 'd3';
import * as topojson from "topojson-client";
import { QueryExecutorProvider } from '../../providers/query-executor/query-executor';
import { LoadingController } from 'ionic-angular';

/**
 * Generated class for the StateOfHealthMapComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'state-of-health-map',
  templateUrl: 'state-of-health-map.html'
})
export class StateOfHealthMapComponent implements OnInit{

  @Input('text') text: string;
  @Input('stateData') stateData:any;
  @Input('selectedElement')selectedElement:any;
  @Input('locationLevel')locationLevel:any;
  @Input('selectedTabData')selectedTabData:any;
  @Input('randomMapId')randomMapId:any;
  @Output() onSelectLocation: EventEmitter<any> = new EventEmitter();
  mapCode: any = 'polygons';
  mapPropertyName: any = 'district';
  isDistrictMapFound: any;
  applicationType = APPLICATION_TYPE;
  constructor(private queryExecutor: QueryExecutorProvider,
    private loading : LoadingController) {
  }
  ngOnInit() {
    let me = this;
    setTimeout(() => {
      me.loadChartByElementName(this.selectedElement.elementName);  
    }, 10);
  }
  public reloadData (stateData, selectedElement, locationLevel, selectedTabData){
    
      this.stateData = stateData;
      this.selectedElement = selectedElement;
      this.locationLevel = locationLevel;
      this.selectedTabData = selectedTabData;
      this.loadChartByElementName(this.selectedElement.elementName);  
  }
  loadChartByElementName(elementName) {
    this.stateData.sort(function (a, b) {
      return b.value - a.value;
    });
    this.loadMap(this.stateData,this.selectedTabData);
  }
  loadMap(stateData1,selectedTabData) {
    let me = this;
    var chartFile = this.getChartDataFile();
    if (!this.isDistrictMapFound || !this.selectedTabData.map.isEnable) {
      document.getElementById(me.randomMapId).innerHTML = "";   // clear previous chart
      return;
    }
    
    var width = 236, height = 200;

    var projection = d3.geoMercator().scale(2);

    var path = d3.geoPath()
      .projection(projection)
      .pointRadius(2);

    document.getElementById(me.randomMapId).innerHTML = "";   // clear previous chart

    var svg = d3.select("#" + me.randomMapId).append("svg")
      .attr("width", width)
      .attr("height", height);

    var g = svg.append("g");


    /*me.localStorage.get(LOCAL_STORAGE.GUJARAT_TOPO_JSON).then(topo => {
      if (topo) {
        setBaseGrapth(topo);
      } else {
        d3.json('http://192.1.200.24:8585/india').then((data) => {
          setBaseGrapth(data);
          me.localStorage.set(LOCAL_STORAGE.GUJARAT_TOPO_JSON, data);
        });
      }
    })*/

    this.queryExecutor.readFile('assets/' + chartFile).subscribe(topo => {
      if (this.locationLevel == LOCATION_LEVEL.DISTRICT) {
        for (let object in topo.objects) {
          //console.info(topo.objects[object].geometries[0].properties.dt_code + '-' + topo.objects[object].geometries[0].properties.dist_name);
        }
      }

      setBaseGrapth(topo);
    }, (data) => {
      console.info(data);
    })
    function setBaseGrapth(data) {
      var boundary = centerZoom(data);
      drawSubUnits(data);
      drawOuterBoundary(data, boundary);
    }

    function centerZoom(data) {

      var o = topojson.mesh(data, data.objects[me.mapCode], function (a, b) { return a === b; });
      projection
        .scale(1)
        .translate([0, 0]);

      var b = path.bounds(o),
        s = 1 / Math.max((b[1][0] - b[0][0]) / width, (b[1][1] - b[0][1]) / height);

      projection
        .scale(s)
        .translate([(width - s * (b[1][0] + b[0][0])) / 2, (height - s * (b[1][1] + b[0][1])) / 2]);

      return o;

    }

    function drawOuterBoundary(data, boundary) {

      g.append("path")
        .datum(boundary)
        .attr("d", path)
        .attr("class", "subunit-boundary")
        .attr("fill", "none")
        .attr("stroke", "#3a403d");

    }

    function getMappingName(name) {
      return NAME_MAPPING_MAP[name] || name;
    }
    function getOpacity(scaledMin, scaledMax, num) {
      var max = Math.max.apply(Math, num);
      var min = Math.min.apply(Math, num);
      return num.map(num => (scaledMax-scaledMin)*(num-min)/(max-min)+scaledMin);
    }
    function updateOpacity(isSmallValuePositive,field){
      let opacity = [];
      let index = 0;
      for(let d of me.stateData){
        opacity[index] = (d[field]);
        index++;
      }
      opacity = getOpacity(0.1,1,opacity);
      index = 0;
      for(let d of me.stateData){
        d.opacity = opacity[index];
        index++;
      }
    }
    function drawSubUnits(data) {
      let mapConfig = selectedTabData.map;
      updateOpacity(mapConfig.isSmallValuePositive,mapConfig.field);
      var subunits = g.selectAll(".subunit")
        .data(topojson.feature(data, data.objects[me.mapCode]).features)
        .enter().append("path")
        .attr("class", "subunit")
        .attr("d", path)
        .style("opacity", function (d: any) {
          for (let stateData of me.stateData) {
            if (d.properties[me.mapPropertyName] && d.properties[me.mapPropertyName].toLowerCase() == getMappingName(stateData.english_name).toLowerCase()) {
              if(mapConfig.isOpacityEnable){
                if(mapConfig.isSmallValuePositive){
                  return stateData.opacity;
                }else{
                  return 1-stateData.opacity;
                }                 
              }else{
                return 0.9;    
              }
            }
          }
          return 0.9;
        })
        .style("fill", function (d: any) {
          for (let stateData of me.stateData) {
            if (d.properties[me.mapPropertyName] && d.properties[me.mapPropertyName].toLowerCase() == getMappingName(stateData.english_name).toLowerCase()) {
              if(stateData.color){
                return stateData.color;
              } else {
                return DEFAULT_MAP_COLOR;
              }
            }
          }
          return DEFAULT_MAP_COLOR;
        })
        .style("stroke", "#fff")
        .style("stroke-width", "1px")
        .on("click", function (d: any) {
          let districtname = d.properties.District
          let element = me.stateData.find(state => districtname.toLowerCase() == getMappingName(state.english_name).toLowerCase());
          if (element && me.applicationType === 'MOBILE')
            me.onSelectLocation.emit(element)
        })
        if(chartFile=="Gujarat-Topo.json"){
        g.selectAll()
          .data(CORPORATION)
          .enter().append("circle")
          .attr("r", 4)
          .style("stroke","white")
          .attr("transform", function(d) {
          return "translate(" + projection([
            d.location.longitude,
            d.location.latitude
          ]) + ")";
          }).style("fill", function (d: any) {
            for (let stateData of me.stateData) {
              if (d.name.toLowerCase() == getMappingName(stateData.english_name).toLowerCase()) {      
                if(stateData.color){
                  return stateData.color;
                } else {
                  return DEFAULT_MAP_COLOR;
                }
              }
            }
          });
        }
      return subunits;
    }
  }
  private getChartDataFile(): string {
    function getMappingName(name) {
      return NAME_MAPPING_MAP[name] || name;
    }
    let isDistrictMapFound = false;
    if (this.locationLevel == LOCATION_LEVEL.STATE) {
      this.mapCode = 'polygons';
      this.mapPropertyName = 'District';
      isDistrictMapFound = true;
      this.isDistrictMapFound = isDistrictMapFound;
      return 'Gujarat-Topo.json';
    } else if (this.locationLevel == LOCATION_LEVEL.DISTRICT) {
      for (let district of STAT_DISTRICT) {
        if (district.name.toLowerCase() == this.selectedElement.english_name.toLowerCase()) {
          this.mapCode = 'District_' + getMappingName(district.name).split(' ')[0];
          this.mapPropertyName = 'Taluka';
          isDistrictMapFound = true;
          this.isDistrictMapFound = isDistrictMapFound;
          return 'Gujarat-State-Topo.json';
        }
      }
    }
    this.isDistrictMapFound = isDistrictMapFound;

  }
  setAspectRatio(array) {
    let scaledMax = 1;
    let scaledMin = 0.1;
    var max = Math.max.apply(Math, array);
    var min = Math.min.apply(Math, array);
    return array.map(num => (scaledMax - scaledMin) * (num - min) / (max - min) + scaledMin);
  }

}



