import { Injectable, ViewChild, Inject } from '@angular/core';
import { LOCAL_STORAGE, VACCINATION_CHANGE_NAME, MIN_LOCATION_LEVEL, ROOT_LOCATION,NAME_MAPPING_MAP,DEFAULT_MAP_COLOR,LINELIST_TYPE,CORPORATION } from '../../app/constants';
import { AlertController, ToastController, App, Events, LoadingController } from 'ionic-angular';
import { StateOfHealthComponent } from '../../components/state-of-health/state-of-health';
import { HomePage } from '../../components/home/home';
import { InMemoryStorageProvider } from '../in-memory-storage/in-memory-storage';
import { QueryExecutorProvider } from '../query-executor/query-executor';
import { TranslateService } from '@ngx-translate/core';
import * as d3 from 'd3';
import * as topojson from "topojson-client";
import { Subject } from 'rxjs';
import { StateOfHealthServiceProvider } from '../state-of-health-service/state-of-health-service';
import { CallNumber } from '@ionic-native/call-number';

/*
  Generated class for the UtilityHelperProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class UtilityHelperProvider {
  selectLocationSubject: Subject<void> = new Subject();
  customObservable = this.selectLocationSubject.asObservable();
  constructor(private altController: AlertController,
    private queryExecutor: QueryExecutorProvider,
    private translate: TranslateService,
    public app: App,
    private loading: LoadingController,
    private callNumber: CallNumber,
    private stateOfHealthService: StateOfHealthServiceProvider,
    private toast: ToastController, private inMemoryStorage: InMemoryStorageProvider) {

  }

  showAlert(title: string, message: string) {
    let alert = this.altController.create({
      title: title,
      message: message,
      buttons: [{
        text: 'Ok'
      }]
    });
    alert.present()
  }
  showToast(msg: string) {
    let toast = this.toast.create({ message: msg, duration: 3000 });
    toast.present();
  }
  isBlank(string: string) {
    return string == null || string == '';
  }
  getLandingComponent(locationLevel): any {
    if (locationLevel == MIN_LOCATION_LEVEL.LOCATION_LEVEL) {
      return StateOfHealthComponent;
    } else {
      return HomePage;
    }
  }

  setRootLocation(locationId){
    if(locationId){
      let query = {
        code: 'get_root_location_by_location_id',
        parameters : {
          location_id : locationId
        }
      }
      this.queryExecutor.execute(query).subscribe(res =>{
        let location = res.result[0];
        if(location){
          ROOT_LOCATION.LOCATION_ID = location.id;
          ROOT_LOCATION.LOCATION_NAME = location.name;
        }
      },eror =>{
        ROOT_LOCATION.LOCATION_ID = 0;
        ROOT_LOCATION.LOCATION_NAME = null;
      })
    }
  }

  onApiFailure(responseError: any, errorMsg?: string){
    let localeObject = this.inMemoryStorage.get(LOCAL_STORAGE.LOCALE);
    if(responseError){
      if (responseError.data && responseError.data.message != null) {
        this.showToast(responseError.data.message);
      } else if(responseError.error && responseError.error.message != null){
        this.showToast(responseError.error.message);
      }else if (responseError.status === 0){
        if(localeObject){
          this.showAlert(this.translate.instant('ErrorMsg'), this.translate.instant('StatusZero'));
        } else {
          this.showAlert('Something went wrong...!!', 'Application is not able to communicate with server, either internet connection/server is down.');
        }
      } else {
        this.showToast(localeObject ? this.translate.instant('ErrorMsg') : 'Something went wrong...!!');
      }
    } else if (errorMsg) {
      this.showToast(errorMsg);
    } else {
      this.showToast(localeObject ? this.translate.instant('ErrorMsg') : 'Something went wrong...!!');
    }
  }
  getBetweenDates(dateFilter: any) {
    const timeLineIntervals = this.inMemoryStorage.get(LOCAL_STORAGE.TIME_LINE_INTERVALS);
    let dateInterval = timeLineIntervals.find(interval => interval.value == dateFilter);
    if(dateInterval){
      let fromDate = new Date(dateInterval.from_date) +" Z";
      return [this.getOnlyDate(new Date(fromDate)), this.getOnlyDate(dateInterval.to_date)];
    }else {
      return [null,null];
    }
  }
  getCurrentFY(){
    let year = new Date().getFullYear();
    if(new Date().getMonth()<3){
      year = year-1;
    }
    return year;
  }
  getBetweenDays(dateFilter: any) {
    const timeLineIntervals = this.inMemoryStorage.get(LOCAL_STORAGE.TIME_LINE_INTERVALS);
    let dateInterval = timeLineIntervals.find(interval => interval.value == dateFilter);
    if (dateInterval) {
      let from = 365;
      const diffTime = Math.abs(dateInterval.from_date - dateInterval.to_date);
      const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24));
      return [from, (from + diffDays)];
    } else {
      return [null, null];
    }
  }
  getOnlyDate(date) {
    if (Number.isInteger(date)) {
      return new Date(date).toISOString().split('T')[0];
    } else {
      return date.toISOString().split('T')[0];
    }

  }
  isLogin() {
    let user = this.inMemoryStorage.get(LOCAL_STORAGE.LOGIN_USER);
    return user != null;
  }
  getFilterMenu(currentFilter){
    const timeLineIntervals = this.inMemoryStorage.get(LOCAL_STORAGE.TIME_LINE_INTERVALS);
    let alert = this.altController.create({
      title: this.translate.instant('DateRange'),
    });
    timeLineIntervals.forEach(interval => {
      alert.addInput({
        type: 'radio',
        label: interval.displayValue,
        value: interval.value,
        checked: currentFilter.value === interval.value
      });
    });
    
    alert.addButton(this.translate.instant('Cancel'));
    return alert;
  }
  copyElement(copyFrom,copyTo){
    copyTo.valueDisp = copyFrom.valueDisp;
    copyTo.value = copyFrom.value;
    copyTo.chart1 = copyFrom.chart1;
    copyTo.chart2 = copyFrom.chart2;
    copyTo.chart3 = copyFrom.chart3;
    copyTo.chart4 = copyFrom.chart4;
    copyTo.days = copyFrom.days
    copyTo.target = copyFrom.target;
  }
  public getTwoDecimal(number){
    if(typeof(number)=="undefined")
    return number;
    return number.toFixed(2);
  }

  public isClickable(element,selectedElementConfig,drillingEnable){
    if(element.level<selectedElementConfig.lineList.lastLevel){
      return 4;
    }else{
      if(drillingEnable && selectedElementConfig.lineList.isEnable){
        if(element[selectedElementConfig.lineList.field]==0 || element[selectedElementConfig.lineList.field]==null){
          return 1;
        }else{
          return 2;
        }
      }else{
        return 3;
      }
    }
  }
  getVaccinationName(vaccination){
    let VACCINATION_NAME = this.inMemoryStorage.get(LOCAL_STORAGE.VACCINATION_NAME);
    let vaccinationName = VACCINATION_NAME[vaccination];
    if(vaccinationName){
      return vaccinationName;
    }else{
      return vaccination;
    }
  }
  getVaccinationChangeName(vaccination){
    let vaccinationName = VACCINATION_CHANGE_NAME[vaccination];
    if(vaccinationName){
      return vaccinationName;
    }else{
      return vaccination;
    }
  }
  formatNumber(value,isYAxis) {
    let ranges = [
      { divider: 1e9, suffix: 'B' },
      { divider: 1e6, suffix: 'M' },
      { divider: 1e3, suffix: 'k' }
    ];
    for (var i = 0; i < ranges.length; i++) {
      if (value >= ranges[i].divider) {
        if (isYAxis) {
          return (value / ranges[i].divider).toFixed(2).toString() + ranges[i].suffix;
        } else {
          return (value / ranges[i].divider).toString() + ranges[i].suffix;
        }
      }
    }
    return value;
  }

  sortAllTypeOfDetailsByFieldAndType(list, field, sortType) {
    list.sort((a, b) => {
      if (a[field] == null) {
        return 1;
      } else if (b[field] == null) {
        return -1;
      } else if (a[field] == b[field]) {
        return 0;
      } else if (sortType) {
        return a[field].toLowerCase().localeCompare(b[field].toLowerCase())
      } else {
        return b[field].toLowerCase().localeCompare(a[field].toLowerCase())
      }
    }
    )
  }

  configureAnLoadMap(randomMapId,data,mapCode,stateData1,selectedElementConfig,mapPropertyName,chartFile){
    let me = this;
    var width = 236, height = 200;
    var projection = d3.geoMercator().scale(2);

    var path = d3.geoPath()
      .projection(projection)
      .pointRadius(2);

    document.getElementById(randomMapId).innerHTML = "";   // clear previous chart

    var svg = d3.select("#" + randomMapId).append("svg")
    .attr("width", width)
    .attr("height", height);

    var g = svg.append("g");

    var boundary = centerZoom(data);
    drawSubUnits(data);
    drawOuterBoundary(data, boundary);

    function centerZoom(data) {

      var o = topojson.mesh(data, data.objects[mapCode], function (a, b) { return a === b; });
      projection
        .scale(1)
        .translate([0, 0]);

      var b = path.bounds(o),
        s = 1 / Math.max((b[1][0] - b[0][0]) / width, (b[1][1] - b[0][1]) / height);

      projection
        .scale(s)
        .translate([(width - s * (b[1][0] + b[0][0])) / 2, (height - s * (b[1][1] + b[0][1])) / 2]);

      return o;

    }

    function drawOuterBoundary(data, boundary) {

      g.append("path")
        .datum(boundary)
        .attr("d", path)
        .attr("class", "subunit-boundary")
        .attr("fill", "none")
        .attr("stroke", "#3a403d");

    }

    function getMappingName(name) {
      return NAME_MAPPING_MAP[name] || name;
    }

    function getOpacity(scaledMin, scaledMax, num) {
      var max = Math.max.apply(Math, num);
      var min = Math.min.apply(Math, num);
      return num.map(num => (scaledMax-scaledMin)*(num-min)/(max-min)+scaledMin);
    }
    function updateOpacity(isSmallValuePositive,field){
      let opacity = [];
      let index = 0;
      for(let d of stateData1){
        opacity[index] = (d[field]);
        index++;
      }
      opacity = getOpacity(0.1,1,opacity);
      index = 0;
      for(let d of stateData1){
        d.opacity = opacity[index];
        index++;
      }
    }
    function drawSubUnits(data) {
      let mapConfig = selectedElementConfig.map;
      updateOpacity(mapConfig.isSmallValuePositive,mapConfig.field);
      var subunits = g.selectAll(".subunit")
        .data(topojson.feature(data, data.objects[mapCode]).features)
        .enter().append("path")
        .attr("class", "subunit")
        .attr("d", path)
        .style("opacity", function (d: any) {
          for (let stateData of stateData1) {
            if (d.properties[mapPropertyName] && d.properties[mapPropertyName].toLowerCase() == getMappingName(stateData.english_name).toLowerCase()) {
              if(mapConfig.isOpacityEnable){
                if(mapConfig.isSmallValuePositive){
                  return stateData.opacity;
                }else{
                  return 1-stateData.opacity;
                }                 
              }else{
                return 0.9;    
              }
            }
          }
          return 0.9;
        })
        .style("fill", function (d: any) {
          for (let stateData of stateData1) {
            if (d.properties[mapPropertyName] && d.properties[mapPropertyName].toLowerCase() == getMappingName(stateData.english_name).toLowerCase()) {
              if(stateData.color){
                return stateData.color;
              } else {
                return DEFAULT_MAP_COLOR;
              }
            }
          }
          return DEFAULT_MAP_COLOR;
        })
        .style("stroke", "#fff")
        .style("stroke-width", "1px")
        .on("click", function (d: any){
          let districtname = d.properties.District
          let element = stateData1.find(state => districtname.toLowerCase() == getMappingName(state.english_name).toLowerCase());
          if (element){
            // me.elementdetails.onSelectLocation(element)
            me.selectLocationSubject.next(element);
          }

        })
        if(chartFile=="Gujarat-Topo.json"){
        g.selectAll()
          .data(CORPORATION)
          .enter().append("circle")
          .attr("r", 4)
          .style("stroke","white")
          .attr("transform", function(d) {
          return "translate(" + projection([
            d.location.longitude,
            d.location.latitude
          ]) + ")";
          }).style("fill", function (d: any) {
            for (let stateData of stateData1) {
              if (d.name.toLowerCase() == getMappingName(stateData.english_name).toLowerCase()) {      
                if(stateData.color){
                  return stateData.color;
                } else {
                  return DEFAULT_MAP_COLOR;
                }
              }
            }
          });
        }
      return subunits;
    }
    
  }

  onShowMoreForMemberList(member,element,elementConfig){
    if(member.isShowMore==undefined){
      if(element.elementName=='FI'){
        if(member.immunisationGiven==null){
          member.immunisationGiven="";
        }  
        if(elementConfig.tabName == 'Fully Immunized'){
        let loading = this.loading.create();
        loading.present();
        this.stateOfHealthService.getRemainingVaccination(member).subscribe(res=>{
          member.vaccination=res.body;
          if(res.status==200){
            member.isShowMore=true;
          }
          loading.dismiss();
        }, (error) => {
          loading.dismiss();
          this.onApiFailure(error);
        });
      }else if(elementConfig.tabName=='Immu. Overdue'){
        if(member.overdueImmunisation){
          member.vaccination = member.overdueImmunisation.split(',');
        }else{
          member.vaccination = [];
        }
        let loading = this.loading.create();
        loading.present();
        this.stateOfHealthService.getRemainingVaccination(member).subscribe(res=>{
          member.otherVaccination=res.body;
          member.otherVaccination = member.otherVaccination.filter(n => !member.vaccination.includes(this.getVaccinationName(n)));
          member.otherVaccination = member.otherVaccination.filter(n => !member.vaccination.includes(this.getVaccinationChangeName(n)));
          if(res.status==200){
            member.isShowMore=true;
          }
          loading.dismiss();
        }, (error) => {
          loading.dismiss();
          this.onApiFailure(error);
        });
      }
        if(member.immunisationGiven){
            member.immunisationGivenTemp=member.immunisationGiven.split(',');
            for(let i=0;i<member.immunisationGivenTemp.length;i++){
              member.immunisationGivenTemp[i]=member.immunisationGivenTemp[i].split('#');     
              member.immunisationGivenTemp[i][1] = member.immunisationGivenTemp[i][1].replace(new RegExp('/', 'g'), '-');
            }
        }
      } else{
        member.isShowMore= true;
      } 
      }else{
        member.isShowMore = !member.isShowMore;
      }
  }
  onCall(no:string){
    this.callNumber.callNumber(no, false)
    .then(res => console.log('Launched dialer!', res))
    .catch(err => console.log('Error launching dialer', err));
  }
}



