import { Component, ViewChild } from '@angular/core';
import { Nav, Platform, Events, ToastController, AlertController, LoadingController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { LoginComponent } from '../components/login/login';
import { LocalStorageProvider } from '../providers/local-storage/local-storage';
import { LOCAL_STORAGE, APPLICATION_TYPE, APP, DATA_QUALITY, DEFAULT_LANG } from './constants';
import { InMemoryStorageProvider } from '../providers/in-memory-storage/in-memory-storage';
import { LoginServiceProvider } from '../providers/login-service/login-service';
import { SohUserServiceProvider } from '../providers/soh-user-service/soh-user-service';
import { UtilityHelperProvider } from '../providers/utility-helper/utility-helper';
import { StateOfHealthComponent } from '../components/state-of-health/state-of-health';
import { AppUpdateServiceProvider } from '../providers/app-update-service/app-update-service';
import { HelpComponent } from '../components/help/help';
import { TranslateService } from '@ngx-translate/core';
import { StateOfHealthWebComponent } from '../components/state-of-health-web/state-of-health-web';
import { AboutUsComponent } from '../components/about-us/about-us';
import { ScreenOrientation } from '@ionic-native/screen-orientation';
import { QueryExecutorProvider } from '../providers/query-executor/query-executor';
import { StateOfHealthServiceProvider } from '../providers/state-of-health-service/state-of-health-service';
import { MapViewComponent } from '../components/map-view/map-view';
import {Covid19ResultsComponent} from './../components/covid19-results/covid19-results';
import { TranslateLSLoader } from './translateLsLoader';
import { LocaleService } from '../providers/locale-sevice/locale-service';
import { forkJoin } from 'rxjs/observable/forkJoin';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;
  rootPage: any;
  dataQuality: any;
  appCurrentVersion: any;
  location: any;
  dateFilter: any = DATA_QUALITY.YEAR_04_2019;
  applicationType = APPLICATION_TYPE;
  updatedLocaleVersion: any;
  menuElement = [];
  isAppInitialize: boolean = false;
  constructor(public platform: Platform,
    public events1: Events,
    public statusBar: StatusBar,
    public splashScreen: SplashScreen,
    public localStorage: LocalStorageProvider,
    public inMemoryStorage: InMemoryStorageProvider,
    public loginService: LoginServiceProvider,
    public sohUserService: SohUserServiceProvider,
    public events: Events,
    public toastCtrl: ToastController,
    private stateOfHealthService: StateOfHealthServiceProvider,
    private queryExecutor: QueryExecutorProvider,
    private loading: LoadingController,
    public alertCtrl: AlertController,
    public utils: UtilityHelperProvider,
    private appVersion: AppUpdateServiceProvider,
    private translate: TranslateService,
    private translateLSLoader : TranslateLSLoader,
    private screenOrientation: ScreenOrientation,
    private localeSerice: LocaleService
  ) {
    platform.ready().then(() => {
      this.InitConfig();
    });
  }
  InitConfig() {
    let loader = this.loading.create();
    this.localStorage.get(LOCAL_STORAGE.LOGIN_USER).then(user => {
      let userId;
      if (user) {
        userId = user.id;
        this.utils.setRootLocation(user.minLocationId);
      }
      this.stateOfHealthService.getElementConfigJsonPermissionWise(userId).subscribe(res => {
        this.stateOfHealthService.setElementConfig(res.body);
        if (res.status == 200) {
          this.setDataQuality();
          let dtos = [];
          dtos.push(this.getInitialQuery('fetch_soh_element_timeline_intervals'));
          dtos.push(this.getInitialQuery('get_soh_vaccine_name'));
          this.queryExecutor.execute({
            code: 'retrieve_system_by_key'
          }).subscribe(res => {
            let localeObject = this.inMemoryStorage.get(LOCAL_STORAGE.LOCALE);
            if (localeObject.localeVersion < Number.parseInt(res.result[0]['key_value'])) {
              this.updatedLocaleVersion = Number.parseInt(res.result[0]['key_value']);
              dtos.push(this.getInitialQuery('get_soh_labels'));
            }
            forkJoin(dtos).subscribe(res=>{
              if(res[0] && res[0]['result'].length > 0){
                let defaultInterval = res[0]['result'].find(x => x.checked === true);
                this.inMemoryStorage.put(LOCAL_STORAGE.TIME_LINE_INTERVALS, res[0]['result']);
                this.inMemoryStorage.put(LOCAL_STORAGE.CORRENT_DATE_FILTER, defaultInterval);
              }
              if(res[1] && res[1]['result'].length > 0){
                let vaccinationName = JSON.parse(res[1]['result'][0]['VACCINATION_NAME']);
                this.inMemoryStorage.put(LOCAL_STORAGE.VACCINATION_NAME, vaccinationName);
              }
              let translationRes =  res.find(query => query['code'] === 'get_soh_labels');
              if(translationRes && translationRes['result'].length > 0){
                console.log('label fetched....');
                let labelObject = [];
                translationRes['result'].forEach(label => {
                  let labels =  JSON.parse(label.labels);
                  labels = labels.reduce(((r, c) => Object.assign(r, c)), {});
                  labelObject.push({
                    lang : label.language,
                    labels : labels
                  })
                });
                this.localeSerice.set(labelObject,this.updatedLocaleVersion).then((data) =>{
                  this.setTranslation(data, user && user.languagePreference ? user.languagePreference : DEFAULT_LANG);
                })
              } else {
                this.setTranslation(localeObject, user && user.languagePreference ? user.languagePreference : DEFAULT_LANG);
              }
            },err=>{
              this.utils.onApiFailure(err);
            })
          },err => {
            this.utils.onApiFailure(err);
          })
        }
        loader.dismiss();
      }, (error) => {
        loader.dismiss();
        this.utils.onApiFailure(error);
      });
      this.events1.subscribe("Data quality", (data) => {
        this.setDataQuality();
      });
      this.events1.subscribe("Change language", (user) => {
        let localeObject = this.inMemoryStorage.get(LOCAL_STORAGE.LOCALE);
        this.setTranslation(localeObject,user.languagePreference);
      });
    });
  }

  setTranslation(data,language){
    let labels = data.labels.find(x => x.lang === language);
    if(!labels){
      labels = data.labels.find(x => x.lang === DEFAULT_LANG);
    }
    this.translateLSLoader.getTranslation(labels.labels).subscribe(value =>{
      this.translate.setDefaultLang(language);
      this.translate.setTranslation(language,value,false);
      this.initializeApp();
      this.isAppInitialize = true;
    })
  }


  registerEvents() {
    this.events.subscribe('openLoginPage', () => {
      console.info('openLoginPage');
      this.rootPage = LoginComponent;
    })
  }

  setDataQuality() {
    let elementsConfig = this.stateOfHealthService.getElementConfig();
    this.menuElement = [];
    // Get all element which we want to display in menu
    elementsConfig.forEach(module => {
      this.menuElement = this.menuElement.concat(module.elements.filter(element => {
        return (element.showInMenu === true);
      }));
    });
    if(this.menuElement.length > 0){
      this.localStorage.get(LOCAL_STORAGE.LOGIN_USER).then(user => {
        if (user) {
          let menuElementsName = this.menuElement.map(element => element.elementName);
          let making = this.loading.create();
          making.present();
          this.queryExecutor.execute({ "code": "get_data_quality_by_time_line_1", "parameters": { "locationId": user.minLocationId, 'elementName': menuElementsName} }
          ).subscribe((data) => {
            if(data.result.length > 0 ){
              this.dataQuality = data.result;
              this.menuElement.forEach(element => {
                element.menuConfig  = this.dataQuality.find(menuElement => menuElement.element_name === element.elementName);
              });
            }
            this.location = user.minLocationName;
            making.dismiss();
          }, (error) => {
            making.dismiss();
          });
        }
      });
    }
  }
  initializeApp() {
    this.checkForMaintenanceMode();
    this.statusBar.styleDefault();
    this.statusBar.styleLightContent();

    if (this.platform.is('cordova')) {
      this.screenOrientation.lock(this.screenOrientation.ORIENTATIONS.PORTRAIT);
      this.rootPage = this.applicationType !== 'MOBILE' ? StateOfHealthWebComponent : StateOfHealthComponent;
    } else {
      this.rootPage = this.applicationType !== 'MOBILE' ? StateOfHealthWebComponent : StateOfHealthComponent;
    }
    if (this.rootPage == StateOfHealthComponent) {
      this.appVersion.isAppUpdate();
    }
    this.registerEvents();
    this.openLandingPage();
    this.setAppVersion();
  }

  checkForMaintenanceMode() {
    let loader = this.loading.create();
    let query = {
      code: 'get_soh_maintenance_mode_1',
      parameters: {
        systemKey: 'SOH_MAINTENANCE_MODE_ENABLE'
      }
    }
    this.queryExecutor.execute(query).subscribe(res => {
      let maintenanceData = res.result[0];
      loader.dismiss();
      if (maintenanceData.is_active && maintenanceData.key_value === 'true') {
        this.showAlertMessage()
      } else {
        return
      }
    }, err => {
      loader.dismiss();
    })
  }

  getInitialQuery(name) {
    let query = {
      code: name
    }
    return this.queryExecutor.execute(query);
  }

  showAlertMessage() {
    let alert = this.alertCtrl.create({
      title: this.translate.instant('UnderMaintenance'),
      message: this.translate.instant('MaintenanceDescription'),
      cssClass: 'tt-alert-box',
      enableBackdropDismiss: false,
      buttons: [
        {
          text: this.translate.instant('MaintenanceButtonText'),
          handler: data => {
            alert.dismiss()
            this.platform.exitApp()
          }
        }
      ]
    });
    alert.present();
  }
  setAppVersion() {
    if (this.platform.is('android')) {
      this.appCurrentVersion = APP.ANDROID_DISPLAY_VERSION;
    } else {
      this.appCurrentVersion = APP.IOS_DISPLAY_VERSION;
    }
  }
  openLandingPage() {
    this.localStorage.get(LOCAL_STORAGE.LOGIN_CODE).then(code => {
      if (code) {
        this.loginService.loginWithLoginCode(code).subscribe(token => {
          this.localStorage.set(LOCAL_STORAGE.TOKEN, token).then(() => {
            this.loginService.getLoginUser().subscribe(user => {
              this.stateOfHealthService.initializeUseApplicationAccessDetails(user);
              this.localStorage.set(LOCAL_STORAGE.LOGIN_USER, user);
              this.localStorage.set(LOCAL_STORAGE.IS_ANONYMOUS_LOGIN, false);
              this.rootPage = this.utils.getLandingComponent(user.minLocationLevel);
            });
          });
        })
      } else {
        this.localStorage.get(LOCAL_STORAGE.TOKEN).then((token => {   // for login user
          if (token) {
            this.inMemoryStorage.put(LOCAL_STORAGE.TOKEN, token);
            this.localStorage.get(LOCAL_STORAGE.LOGIN_USER).then(user => {
              this.stateOfHealthService.initializeUseApplicationAccessDetails(user);
              this.localStorage.set(LOCAL_STORAGE.LOGIN_USER, user);
              this.rootPage = this.utils.getLandingComponent(user.minLocationLevel);
            });
          }
        }));
      }
    });
  }
  getUserLocation() {
    let user = this.inMemoryStorage.get(LOCAL_STORAGE.LOGIN_USER);
    if (user) {
      return user.minLocationName;
    }
  }
  isLogin() {
    let user = this.inMemoryStorage.get(LOCAL_STORAGE.LOGIN_USER);
    return user != null;
  }
  getUserName() {
    let user = this.inMemoryStorage.get(LOCAL_STORAGE.LOGIN_USER);
    if (user) {
      return user.name;
    } else {
      return 'Guest';
    }
  }
  onAboutUs() {
    this.nav.push(AboutUsComponent);
  }
  logout() {
    let localeObject = this.inMemoryStorage.get(LOCAL_STORAGE.LOCALE);
    this.setTranslation(localeObject,DEFAULT_LANG);
    this.loginService.logout(this.getUserName());
    this.nav.setRoot(LoginComponent);
  }
  logIn() {
    this.nav.push(LoginComponent, { isBackButtonEnable: true });
  }
  onHelp() {
    this.nav.push(HelpComponent);
  }
  map() {
    this.nav.push(MapViewComponent, { isBackButtonEnable: true });
  }
  covid19Details(){
    this.nav.push(Covid19ResultsComponent);
  }
  public getFile(elementConfig){
    return this.stateOfHealthService.getFile(elementConfig);  
  }
}



