import { Component, ViewChild, ElementRef, Input, OnChanges } from '@angular/core';
import { QueryExecutorProvider } from '../../providers/query-executor/query-executor';
import { LoadingController, AlertController } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';
declare var google, MarkerClusterer;

/**
 * Generated class for the MapComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'map',
  templateUrl: 'map.html'
})
export class MapComponent implements OnChanges {
  @ViewChild('googleMap') mapElement: ElementRef;
  showCount: boolean;

  map: any;
  geoJsonData: any;
  heatMapData = [];
  markersData = [];
  locations = [];
  heatMapView: boolean;
  caseType: string = 'POSITIVE_CASES';
  caseValue: string = 'Positive cases';
  winInfo: any;
  heatmap: any;
  InfoWindow: any;
  Latlngbounds: any;
  case: any = {
    key: this.caseType,
    value: this.caseValue
  };
  casesTypes = [
    {
      key: 'POSITIVE_CASES',
      value: 'Positive cases'
    },
    {
      key: 'SYMPTOMATIC_CASES',
      value: 'All cases'
    },
    {
      key: 'TRAVEL_HISTORY_CASES',
      value: 'Domestic Travellers With Symptoms'
    },
    {
      key: 'INT_TRAVEL_HISTORY_CASES',
      value: 'International Travellers With Symptoms'
    },
    {
      key: 'NO_TRAVEL_HISTORY_CASES',
      value: 'Non Travellers With Symptoms'
    }
  ];

  constructor(
    private queryExecutor: QueryExecutorProvider,
    private loading: LoadingController,
    public http: HttpClient,
    private altController: AlertController
  ) { }

  ngOnInit() {
    this.getGujaratLineJson();
  }

  ngOnChanges() { }

  initializeGoogleMap() {
    // Set Gujarat as center of the map 
    const gujarat = new google.maps.LatLng(22.2587, 71.1924);
    // Define the LatLng coordinates for the outer path.
    this.map = new google.maps.Map(this.mapElement.nativeElement, {
      center: gujarat,
      zoom: 7,
      mapTypeId: google.maps.MapTypeId.ROADMAP,
      mapTypeControl: false,
      streetViewControl: false
    });
    this.map.data.setStyle({
      fillColor: '#9dc4eb',
      strokeWeight: 1,
      strokeOpacity: 0.8,
      strokeColor: '#767676'
    });
    this.map.data.addGeoJson(this.geoJsonData);
  }

  initHeatMap() {
    this.initializeGoogleMap();
    this.winInfo = new google.maps.InfoWindow({ maxWidth: 200 });
    if (this.showCount) {
      const image = {
        url: 'assets/imgs/orange-cluster.png',
        scaledSize: new google.maps.Size(50, 50), // scaled size
        origin: new google.maps.Point(0, 0), // origin
        anchor: new google.maps.Point(0, 0) // anchor
      };

      const markers = this.locations.map(function (location, i) {
        const label = {
          text: "" + location.count + "",
          color: 'black',
          fontSize: '11px',
          fontFamily: "Arial,sans-serif",
          fontWeight: 'bold',
          fontStyle: 'normal',
          textAlign: 'center',
          lineHeight: '55px',
        };
        const marker = new google.maps.Marker({
          position: location.location,
          label: label,
          weight: location.count,
          icon: image
        });
        return marker;
      });

      const calc = function (markers, numStyles) {
        let weight = 0;
        for (var i = 0; i < markers.length; ++i) {
          weight += Number(markers[i].weight);
        }
        return {
          text: weight,
          index: Math.min(String(weight).length, numStyles)
        };
      }
      // Add a marker clusterer to manage the markers.it will group nearby loation and dsplay count of that area.
      const markerCluster = new MarkerClusterer(this.map, null, { imagePath: 'assets/imgs/m' });
      markerCluster.setCalculator(calc);
      markerCluster.addMarkers(markers);
    }
    this.heatmap = new google.maps.visualization.HeatmapLayer({
      data: this.heatMapData,
      radius: 25,
    });
    this.heatmap.setMap(this.map);
  }

  initMarkersMap(markers) {
    this.initializeGoogleMap();

    //Initializing the InfoWindow, Map and LatLngBounds objects.
    this.InfoWindow = new google.maps.InfoWindow();
    this.Latlngbounds = new google.maps.LatLngBounds();

    //Looping through the Array and adding Markers.
    for (var i = 0; i < markers.length; i++) {
      const data = markers[i];
      const myLatlng = new google.maps.LatLng(data.latitude, data.longitude);

      //Initializing the Marker object.
      const marker = new google.maps.Marker({
        position: myLatlng,
        map: this.map,
        title: data.title
      });

      //Adding InfoWindow to the Marker.
      ((marker, data) => {
        google.maps.event.addListener(marker, "click", (e) => {
          this.InfoWindow.setContent("<div class='heat-map-popup'><b>" + data.description.replace(/\r?\n/g, "<br />") + "</b></div>");
          this.InfoWindow.open(this.map, marker);
        });
      })(marker, data);

      //Plotting the Marker on the Map.
      this.Latlngbounds.extend(marker.position);
    }
  }

  getGujaratLineJson() {
    const loader = this.loading.create();
    loader.present();
    this.http.get('assets/Gujarat-line.json')
      .subscribe((res: any) => {
        loader.dismiss();
        this.geoJsonData = res;
        this.getHeatMapData();
      }, (error) => {
        loader.dismiss();
        console.log(error);
      });
  }

  getMarkersData() {
    this.heatMapView = false;
    const loader = this.loading.create();
    loader.present();
    const dto = {
      code: 'covid_case_markings_retrieval',
      parameters: {
      }
    };
    this.queryExecutor.execute(dto)
      .subscribe((res) => {
        this.markersData = res.result;
        loader.dismiss();
        this.initMarkersMap(this.markersData);
      }, (error) => {
        loader.dismiss();
        console.log(error);
      });
  }

  getHeatMapData(queryCode = null) {
    this.heatMapView = true;
    const loader = this.loading.create();
    loader.present();
    if (!queryCode) {
      queryCode = 'covid_heat_map_data';
      this.caseType = 'POSITIVE_CASES';
      this.caseValue = 'Positive cases';
    }
    const dto = {
      code: queryCode,
      parameters: {
      }
    };
    this.heatMapData = [];
    this.locations = [];
    this.heatMapView = true;
    this.queryExecutor.execute(dto)
      .subscribe((res) => {
        const heapMapDataResponse = res.result;
        heapMapDataResponse.forEach((data) => {
          if (data && data.weight !== null) {
            this.heatMapData.push({ location: new google.maps.LatLng(data.latitude, data.longitude), weight: data.weight });
          } else {
            this.heatMapData.push(new google.maps.LatLng(data.latitude, data.longitude));
          }
          this.locations.push({ location: { lat: Number(data.latitude), lng: Number(data.longitude) }, count: data.weight });
        });
        loader.dismiss();
        this.initHeatMap();
      }, (error) => {
        loader.dismiss();
        console.log(error);
      });
  }

  changeHeatMap(caseType) {
    this.caseType = caseType;
    const caseObj = this.casesTypes.filter((c: any) => c.key == this.caseType);
    this.caseValue = caseObj[0].value;
    switch (this.caseType) {
      case 'POSITIVE_CASES':
        this.getHeatMapData('covid_heat_map_data');
        break;
      case 'SYMPTOMATIC_CASES':
        this.getHeatMapData('idsp_heat_map_all_travellers');
        break;
      case 'TRAVEL_HISTORY_CASES':
        this.getHeatMapData('idsp_heat_map_domestic_trav');
        break;
      case 'INT_TRAVEL_HISTORY_CASES':
        this.getHeatMapData('idsp_heat_map_int_trav');
        break;
      case 'NO_TRAVEL_HISTORY_CASES':
        this.getHeatMapData('idsp_heat_map_no_trav');
        break;
      default:
        break;
    }
  }

  public showCase() {
    const alert = this.altController.create({
      title: 'Case Type'
    });

    this.casesTypes.forEach((caseObject: any) => {
      alert.addInput({
        type: 'radio',
        label: caseObject.value,
        value: caseObject.key,
        checked: caseObject.key == this.caseType
      });
    });
    alert.addButton('Cancel');
    alert.addButton({
      text: 'OK',
      handler: mapType => {
        this.changeHeatMap(mapType);
      }
    });
    alert.present();
  }

}



