import { Injectable } from '@angular/core';
import { AlertController, Platform } from 'ionic-angular';
import { AppVersion } from '@ionic-native/app-version';
import { QueryExecutorProvider } from '../query-executor/query-executor';
import { Market } from '@ionic-native/market';
import { MARCKET_PLACE_ID, APP } from '../../app/constants';
import { TranslateService } from '@ngx-translate/core';

/*
  Generated class for the AppUpdateServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class AppUpdateServiceProvider {

  constructor(public alertCtrl: AlertController,
    public platform: Platform,
    private appVersion: AppVersion,
    private market: Market,
    private translate: TranslateService,
    private queryExecutor: QueryExecutorProvider) {

  }

  public isAppUpdate() {
    this.isAppUpdateRequires();
  }
  public getAppVersion(): any {
    if (this.platform.is('android') || this.platform.is('ios')) {
      this.appVersion.getVersionCode().then(code => {
        return code;
      });
    } else {
      return '';
    }
  }
  private isAppUpdateRequires() {
    if (this.platform.is('android')) {
      this.queryExecutor.execute({ "code": "soh_get_app_current_version", "parameters": {} }).subscribe(version => { 
        if(APP.ANDROID_CODE_VERSION < Number.parseInt(version.result[1].androidVersion)){
          this.askForUpdate();
        }else if (APP.ANDROID_CODE_VERSION < Number.parseInt(version.result[0].androidVersion)) {
          this.askForNotRequiredUpdate();
        }
      });
    } else {
      this.queryExecutor.execute({ "code": "soh_get_app_current_version_ios", "parameters": {} }).subscribe(version => {
        if (APP.IOS_CODE_VERSION < Number.parseInt(version.result[1].iosVersion)) {
          this.askForUpdate();
        }else if (APP.IOS_CODE_VERSION < Number.parseInt(version.result[0].iosVersion)) {
          this.askForNotRequiredUpdate();
        }
      });
    }
  }
  
  askForUpdate() {
    let alert = this.alertCtrl.create({
      title: this.translate.instant('UpdateAvailable'),
      message: this.translate.instant('UpdateDescription'),
      cssClass: 'tt-alert-box',
      enableBackdropDismiss: false,
      buttons: [
        {
          text: this.translate.instant('Update'),
          handler: data => {
            this.handleMarket();
          }
        }
      ]
    });
    alert.present();
  }
  askForNotRequiredUpdate(){
    let alert = this.alertCtrl.create({
      title: this.translate.instant('UpdateAvailable'),
      message: this.translate.instant('UpdateDescription'),
      cssClass: 'update',
      enableBackdropDismiss: false,
      buttons: [
        {
          text: this.translate.instant('Update'),
          handler: data => {
            this.handleMarket();
          }
        },
        {
          text: this.translate.instant('Skip'),
          role: 'cancel'
        }
      ]
    });
    alert.present();
  }
  handleMarket(){
    if (this.platform.is('android')) {
      this.market.open(MARCKET_PLACE_ID.ANDROID);
    } else {
      this.market.open(MARCKET_PLACE_ID.IOS);
    }
  }
}



