import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { API_HOST } from '../../app/constants';

/*
  Generated class for the SohUserServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class SohUserServiceProvider {

  constructor(public http: HttpClient) {
    console.log('Hello SohUserServiceProvider Provider');
  }

  public activeCode(code:string):Observable<any>{
    return this.http.get(API_HOST+`/api/mobile/activeCode?code=${code}`);
  }

  public sohRegisterSendOTP(user:any):Observable<any>{
    return this.http.post(API_HOST+`/api/mobile/sohRegisterSendOTP`,user);
  }
  public sohRegister(user:any):Observable<any>{
    return this.http.post(API_HOST+`/api/mobile/sohRegister`,user);
  }

}



