import { Component, Input } from '@angular/core';
import { CallNumber } from '@ionic-native/call-number';
import { MemberLineListComponent } from '../member-line-list/member-line-list';
import { NavController, Events} from 'ionic-angular';
import { LINELIST_TYPE } from '../../app/constants';

/**
 * Generated class for the FieldDetailsComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'field-details',
  templateUrl: 'field-details.html'
})
export class FieldDetailsComponent {
  @Input('fieldData')fieldData:any;
  @Input('elementConfig') elementConfig:any;
  @Input('selectedTabData') selectedTabData:any;
  @Input('selectedDateFilter') selectedDateFilter:any;
  @Input('drillingEnable') drillingEnable:any;
  @Input('isCmDashBoard') isCmDashBoard:any;
  userDetails:any;

  constructor(public callNumber: CallNumber,
    public navCtrl: NavController,
    public event: Events){
  } 
  ngOnChanges(){
    for(let field of this.selectedTabData.fields){
      if(field.fieldType=='userDetail' && this.fieldData[field.fieldName] && IsJsonString(this.fieldData[field.fieldName])){
        this.userDetails=JSON.parse(this.fieldData[field.fieldName]);
      }
    }
    function IsJsonString(str) {
      try {
          JSON.parse(str);
      } catch (e) {
          return false;
      }
      return true;
    }
  }
  public openLineList(field){
    if(!this.isCmDashBoard){
      this.navCtrl.push(MemberLineListComponent,
        { elementName: this.selectedTabData.elementName, locationId: this.fieldData.locationId, element: this.fieldData, selectedDateFilter:this.selectedDateFilter,selectedElementConfig:this.selectedTabData, fieldConfig:field , type:LINELIST_TYPE.FIELD});
    }else{
      this.event.publish("CMDashBoardLineList",field,this.fieldData)
    }
  }
  public onCall(no:string){
    this.callNumber.callNumber(no, false)
    .then(res => console.log('Launched dialer!', res))
    .catch(err => console.log('Error launching dialer', err));
  }
}



