import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
//import { GujaratComponent } from '../components/gujarat/gujarat';
import { StateOfHealthComponent } from '../components/state-of-health/state-of-health';
import { StateOfHealthDetailsComponent } from '../components/state-of-health-details/state-of-health-details';
import { HttpClientModule, HTTP_INTERCEPTORS, HttpClient } from '@angular/common/http';
//import { LoginComponent } from '../components/login/login';
import { LoginServiceProvider } from '../providers/login-service/login-service';
import { LocalStorageProvider } from '../providers/local-storage/local-storage';
import { InMemoryStorageProvider } from '../providers/in-memory-storage/in-memory-storage';
//import { AppUpdate } from '@ionic-native/app-update';
import { IonicStorageModule } from '@ionic/storage';
import { CustomInterceptor } from './interceptor/CustomInterceptor';
import { QueryExecutorProvider } from '../providers/query-executor/query-executor';
import { UtilityHelperProvider } from '../providers/utility-helper/utility-helper';
import { ComponentsModule } from '../components/components.module';
import { LoginComponent } from '../components/login/login';
import { RegisterFormComponent } from '../components/register-form/register-form';
import { SohUserServiceProvider } from '../providers/soh-user-service/soh-user-service';
import { LoginCodeComponent } from '../components/login-code/login-code';
import { MemberLineListComponent } from '../components/member-line-list/member-line-list';
import { HomePage } from '../components/home/home';
import { AppVersion } from '@ionic-native/app-version';
import { AppUpdateServiceProvider } from '../providers/app-update-service/app-update-service';
import { Market } from '@ionic-native/market';
import { CallNumber } from '@ionic-native/call-number';
import { HelpComponent } from '../components/help/help';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { createTranslateLoader } from './createTranslateLoader';
import { StateOfHealthWebComponent } from '../components/state-of-health-web/state-of-health-web';
import { StateOfHealthServiceProvider } from '../providers/state-of-health-service/state-of-health-service';
import { PipesModule } from '../pipes/pipes.module';
import { AboutUsComponent } from '../components/about-us/about-us';
import { ScreenOrientation } from '@ionic-native/screen-orientation';
import { FieldDetailsComponent } from '../components/field-details/field-details';
import { PieChartComponent } from '../components/pie-chart/pie-chart';
import { ElementDetailsComponent } from '../components/element-details/element-details'
import { LineChartComponent } from '../components/line-chart/line-chart';
import { Uid } from '@ionic-native/uid';
import { AndroidPermissions } from '@ionic-native/android-permissions';
import { MapViewComponent } from '../components/map-view/map-view';
import { MapComponent } from '../components/map/map';
import { DecimalPipe } from '@angular/common';
import { StatusReportComponent } from '../components/status-report/status-report';
import { Covid19ResultsComponent } from '../components/covid19-results/covid19-results';
import { MemberHealthInfraLineListComponent } from '../components/member-health-infra-line-list/member-health-infra-line-list';
import { TranslateLSLoader } from './translateLsLoader';
import { LocaleService } from '../providers/locale-sevice/locale-service';


@NgModule({
  declarations: [
    MyApp,
    HomePage,
    //GujaratComponent,
    LoginComponent,
    StateOfHealthComponent,
    StateOfHealthDetailsComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    PipesModule,
    IonicModule.forRoot(MyApp),
    ComponentsModule,
    IonicStorageModule.forRoot(),
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (createTranslateLoader),
        useClass: TranslateLSLoader,
        deps: [HttpClient]
      }
    })
  ],
  exports: [TranslateModule],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    LoginComponent,
    MapViewComponent,
    //GujaratComponent,
    StateOfHealthComponent,
    StateOfHealthDetailsComponent,
    RegisterFormComponent,
    LoginCodeComponent,
    MemberLineListComponent,
    MemberHealthInfraLineListComponent,
    FieldDetailsComponent,
    PieChartComponent,
    HelpComponent,
    AboutUsComponent,
    Covid19ResultsComponent,
    StateOfHealthWebComponent,
    ElementDetailsComponent,
    LineChartComponent,
    MapComponent,
    StatusReportComponent
  ],
  providers: [
    StatusBar,
    SplashScreen,
    AppVersion,
    Market,
    CallNumber,
    ScreenOrientation,
    // AppUpdate,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    LoginServiceProvider,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: CustomInterceptor,
      multi: true
    },
    LocalStorageProvider,
    InMemoryStorageProvider,
    QueryExecutorProvider,
    UtilityHelperProvider,
    SohUserServiceProvider,
    AppUpdateServiceProvider,
    Uid,
    AndroidPermissions,
    StateOfHealthServiceProvider,
    ElementDetailsComponent,
    LineChartComponent,
    MapComponent,
    DecimalPipe,
    TranslateLSLoader,
    LocaleService
  ]
})
export class AppModule { }



