import { Component, ViewChild } from '@angular/core';
import { NavController, NavParams, LoadingController } from 'ionic-angular';
import { LocalStorageProvider } from '../../providers/local-storage/local-storage';
import { LOCATION_LEVEL, TIME_LINE, ROOT_LOCATION} from '../../app/constants';
import { QueryExecutorProvider } from '../../providers/query-executor/query-executor';
import { HttpClient } from '@angular/common/http';
import { UtilityHelperProvider } from '../../providers/utility-helper/utility-helper';
import { InMemoryStorageProvider } from '../../providers/in-memory-storage/in-memory-storage';
import { LOCAL_STORAGE } from '../../app/constants';
import { StateOfHealthComponent } from '../state-of-health/state-of-health';
import { StateOfHealthServiceProvider } from '../../providers/state-of-health-service/state-of-health-service';
import { ElementDetailsComponent } from '../element-details/element-details';
import { StatusReportComponent } from '../status-report/status-report';
import { Covid19ResultsComponent } from '../covid19-results/covid19-results';
import { Subscription } from 'rxjs';

/**
 * Generated class for the StateOfHealthDetailsComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'state-of-health-details',
  templateUrl: 'state-of-health-details.html',

})
export class StateOfHealthDetailsComponent {
  stateData: any = [];
  selectedElement: any;
  elements: any;
  chartData: any;
  locationLevel: any;
  isDistrictMapFound: any;
  HEALTH_ELEMENTS: any;
  mapCode: any = 'polygons';
  mapPropertyName: any = 'district';
  randomMapId: any;
  drillingEnable: any;
  selectedDateFilter: any;
  nextElement: any;
  selectedElementHelp: string;
  filterChangeFun: any;
  elementsConfig:any;
  selectedElementConfig:any;
  selectedTabData:any;
  tabName:any;
  timeLineLabel:any;
  heading1:any;
  heading2:any;
  totalHeading:any
  userId: any;
  dateFilterOptions: any;
  subscription:Subscription;
  @ViewChild(ElementDetailsComponent) elementdetails: ElementDetailsComponent;
  constructor(public navCtrl: NavController,
    public localStorage: LocalStorageProvider,
    private queryExecutor: QueryExecutorProvider,
    private navParam: NavParams,
    private loading: LoadingController,
    public http: HttpClient,
    public inMemoryStorage: InMemoryStorageProvider,
    private utilityHelper: UtilityHelperProvider,
    private stateOfHealthService: StateOfHealthServiceProvider
  ) {
    this.elements = this.navParam.get('elements');
    this.selectedElement = this.navParam.get('element');
    this.locationLevel = this.navParam.get('locationLevel');
    this.drillingEnable = this.navParam.get('drillingEnable');
    this.selectedDateFilter = this.navParam.get('dateFilter');
    this.selectedElementConfig = this.navParam.get('selectedElementConfig');
    this.elementsConfig = this.stateOfHealthService.getElementConfig();
    this.tabName = this.navParam.get('tabName');
    this.dateFilterOptions = this.inMemoryStorage.get(LOCAL_STORAGE.TIME_LINE_INTERVALS);
    for(let tab of this.selectedElementConfig.tabs){
      if(this.tabName==tab.tabName){
        this.selectedTabData = tab;
        this.totalHeading = tab.noOfHeading || 1;
        console.log(tab)
        this.timeLineLabel=tab.elementDisplayNamePostfix.replace(`{${TIME_LINE}}`,this.selectedDateFilter.displayValue);
        this.heading1=tab.headingLabel1 ? tab.headingLabel1.replace(`{${TIME_LINE}}`,this.selectedDateFilter.displayValue) : tab.headingLabel1;
        this.heading2=tab.headingLabel2 ? tab.headingLabel2.replace(`{${TIME_LINE}}`,this.selectedDateFilter.displayValue) : tab.headingLabel2;
      }
    }
    if (!this.locationLevel) {
      this.locationLevel = LOCATION_LEVEL.STATE;
    }
    this.randomMapId = "Map-" + Math.floor(Math.random() * 100) + 1;
  }
  ionViewDidLoad() {
    let user = this.inMemoryStorage.get(LOCAL_STORAGE.LOGIN_USER);
    if (user) {
      this.userId = user.id;
    }
    this.subscription = this.utilityHelper.customObservable.subscribe((element)=>{
      this.elementdetails.onSelectLocation(element);
    })
    this.loadData();
    this.setElements();
  }
  ngOnDestroy(){
    this.subscription.unsubscribe();
  }
  ionViewWillUnload() {
    console.info('Filter change unsubscribe');
  }
  onFilterChange(newFilter) {
    let me = this;
    let selectedDateFilter = this.dateFilterOptions.find(interval => interval.value === newFilter);
      this.timeLineLabel=this.selectedTabData.elementDisplayNamePostfix.replace(`{${TIME_LINE}}`,selectedDateFilter.displayValue);
      this.heading1=this.selectedTabData.headingLabel1 ? this.selectedTabData.headingLabel1.replace(`{${TIME_LINE}}`,selectedDateFilter.displayValue) : this.selectedTabData.headingLabel1;
      this.heading2=this.selectedTabData.headingLabel2 ? this.selectedTabData.headingLabel2.replace(`{${TIME_LINE}}`,selectedDateFilter.displayValue) : this.selectedTabData.headingLabel2;
      me.onDateFilterChange(selectedDateFilter);
      me.loadPreviousElement(this.selectedElement.locationId);
  }
  onDateFilterChange(newFilter) {
    this.selectedDateFilter = newFilter;
    this.loadData();
  }
  setElements(){
    let making = this.loading.create();
    making.present();
    this.queryExecutor.execute({ "code": "get_elements_details_permission_wise_3", "parameters": { "locationId": this.selectedElement.locationId || ROOT_LOCATION.LOCATION_ID, 'timeline_type' : this.selectedDateFilter.value, 'user_id': this.userId }}
    ).subscribe((data) => {
      this.elements = this.stateOfHealthService.prepareData(data.result);    
      this.selectedElement = this.elements[this.selectedTabData.elementName]        
      making.dismiss();
    }, (error) => {
      making.dismiss();
    })
  }
  setTabData(data){
    this.selectedTabData = data;
    this.tabName = data.tabName;
    this.stateData = this.chartData;
    this.selectedElement = this.elements[this.selectedTabData.elementName];
    this.loadData();
    this.totalHeading = this.selectedTabData.noOfHeading || 1;
    this.timeLineLabel=this.selectedTabData.elementDisplayNamePostfix.replace(`{${TIME_LINE}}`,this.selectedDateFilter.displayValue);
    this.heading1=this.selectedTabData.headingLabel1 ? this.selectedTabData.headingLabel1.replace(`{${TIME_LINE}}`,this.selectedDateFilter.displayValue) : this.selectedTabData.headingLabel1;
    this.heading2=this.selectedTabData.headingLabel2 ? this.selectedTabData.headingLabel2.replace(`{${TIME_LINE}}`,this.selectedDateFilter.displayValue) : this.selectedTabData.headingLabel2;
      setTimeout(()=>{
      this.elementdetails.loadMap(this.selectedTabData);
      if(this.elementdetails.piechart!=null){
        this.elementdetails.reloadPieChart(this.selectedElement,this.selectedTabData);
      }
    },10);    
  }
  loadData() {
    let loading = this.loading.create();
    loading.present();
    this.queryExecutor.execute({ "code": "state_of_health_data_retrieval_15", "parameters": { "locationId": this.selectedElement.locationId, 'timeline_type': this.selectedDateFilter.value , 'element_name': this.selectedTabData.elementName} }).subscribe(response => {
      this.chartData = response.result;
      this.stateData = this.chartData;
      this.randomMapId = "Map-" + Math.floor(Math.random() * 100) + 1;
      if (this.locationLevel == LOCATION_LEVEL.STATE
        || this.locationLevel == LOCATION_LEVEL.DISTRICT)
        this.loadChartByElementName(this.selectedElement.elementName);
      this.updateNextElementValue();
      this.selectedElement.currentLevel = this.stateData[0].level;
      this.elementdetails.loadMap(this.selectedTabData);

      loading.dismiss();
    }, (error) => {
      loading.dismiss();
      this.utilityHelper.onApiFailure(error);
    })
  }
  updateNextElementValue() {
    console.info(this.stateData);
    if (this.selectedElement) {
      this.stateData.forEach(newElement => {
        if (newElement.elementName == this.selectedElement.elementName && newElement.locationId == this.selectedElement.locationId) {
          this.utilityHelper.copyElement(newElement, this.selectedElement);
        }
      });
    }

  }
  onSelectElement(elementName) {
    if(elementName === "COVID_STATUS_REPORT"){
      this.navCtrl.push(StatusReportComponent);
      return;
    } else if(elementName === "COVID19_HEALTH_INFRA_REPORT"){
      this.navCtrl.push(Covid19ResultsComponent);
      return;
    }
    for(let moduleConfig of this.elementsConfig){
      if(moduleConfig.module==this.selectedElementConfig.module){
        for(let elementConfig of moduleConfig.elements){
          if(elementConfig.elementName==elementName){
            this.selectedElementConfig = elementConfig;
          }
        }
      }
    }
    this.selectedTabData= this.selectedElementConfig.tabs[0]; 
    this.totalHeading = this.selectedTabData.noOfHeading || 1;
    this.timeLineLabel=this.selectedTabData.elementDisplayNamePostfix.replace(`{${TIME_LINE}}`,this.selectedDateFilter.displayValue);
    this.heading1=this.selectedTabData.headingLabel1 ? this.selectedTabData.headingLabel1.replace(`{${TIME_LINE}}`,this.selectedDateFilter.displayValue) : this.selectedTabData.headingLabel1;
    this.heading2=this.selectedTabData.headingLabel2 ? this.selectedTabData.headingLabel2.replace(`{${TIME_LINE}}`,this.selectedDateFilter.displayValue) : this.selectedTabData.headingLabel2;
    this.tabName = this.selectedTabData.tabName;
    if(this.elements[elementName] && this.elements[elementName].english_name==this.selectedElement.english_name && this.elements[elementName].days==this.selectedElement.days){
      this.selectedElement = this.elements[this.selectedElementConfig.tabs[0].elementName];
      if(this.elementdetails.piechart!=null && this.selectedTabData.pieChart.isEnable){ 
        this.elementdetails.reloadPieChart(this.selectedElement,this.selectedTabData);
      }
    }else{
    this.loadElements(this.selectedElement.locationId,elementName);
  }
    this.loadData();
    if(this.elementdetails!=null  && this.selectedTabData.map.isEnable){
      this.elementdetails.loadMap(this.selectedTabData);
    }
    this.loadChartByElementName(this.selectedElement.elementName);
  }
  loadChartByElementName(elementName) {
    this.stateData.sort(function (a, b) {
      return b.value - a.value;
    }) 
  }
  loadPreviousElement(locationId) {
    this.loadElements(locationId,this.selectedElement.elementName);
  }
  loadElements(locationId,elementName){
    let making = this.loading.create();
    making.present();
    this.queryExecutor.execute({ "code": "get_elements_details_permission_wise_3", "parameters": { "locationId": locationId || ROOT_LOCATION.LOCATION_ID, 'timeline_type' : this.selectedDateFilter.value, 'user_id': this.userId }}
    ).subscribe((data) => {
      this.elements = this.stateOfHealthService.prepareData(data.result);
      this.selectedElement = this.elements[this.selectedTabData.elementName];
      if(this.elementdetails!=null  && this.selectedTabData.map.isEnable){
        this.elementdetails.loadMap(this.selectedTabData);
      }
      if(this.elementdetails.piechart!=null && this.selectedTabData.pieChart.isEnable){
        this.elementdetails.reloadPieChart(this.selectedElement,this.selectedTabData);
      }
      making.dismiss();
    }, (error) => {
      making.dismiss();
    })
  }

  public onMenuClick() {
    let alert = this.utilityHelper.getFilterMenu(this.selectedDateFilter);
    alert.addButton({
      text: 'OK',
      handler: filter => {
        this.onFilterChange(filter);  
      }
    });
    alert.present();
  }
  onHomePage() {
    let user = this.inMemoryStorage.get(LOCAL_STORAGE.LOGIN_USER);
    if(user){
      this.navCtrl.setRoot(this.utilityHelper.getLandingComponent(user.minLocationLevel));
    }else{
      this.navCtrl.setRoot(StateOfHealthComponent);
    }
  }
  public isLogin() {
    return this.utilityHelper.isLogin();
  }
  public getFile(elementConfig){
    return this.stateOfHealthService.getFile(elementConfig);  
  }
}



