import { Component, Input} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import * as d3 from 'd3';
import { DecimalPipe } from '@angular/common';

/**
 * Generated class for the PieChartComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'pie-chart',
  templateUrl: 'pie-chart.html'
})
export class PieChartComponent{

  @Input('element') element:any;
  @Input('elementConfig') elementConfig:any;

  constructor(private translateService: TranslateService,private _decimalPipe: DecimalPipe) {
  }

  ngOnInit() {
    let me = this;
    setTimeout(() => {
      me.loadChartByElement();  
    }, 100);
  }
  public reloadData (selectedElement,elementConfig){
      this.element = selectedElement;
      this.elementConfig = elementConfig;
      this.loadChartByElement();  
  }

  loadChartByElement(){
      let dataset = [];
      let color = [];
      let total = 0;
      for(let field of this.elementConfig.pieChart.fields){
        total = total + this.element[field.field];
        color.push(field.color);
      }
      for(let field of this.elementConfig.pieChart.fields){
        let data = { label : this.translateService.instant(field.name), count : this.element[field.field], presentage:+((this.element[field.field] * 100) / total).toFixed(2)};
        dataset.push(data);
      }
      this.pieChart(dataset,this.elementConfig.elementName,color);
      
  }

  public pieChart(dataset,elementName,color) {    //Ref : https://embed.plnkr.co/plunk/T4suS0 

    var width = 250;
    var height = 250;
    var radius = Math.min(width, height) / 2;
    var donutWidth = 20;
    var legendRectSize = 18;                                  // NEW
    var legendSpacing = 5;                                    // NEW

    color = d3.scaleOrdinal(color);
    
    document.getElementById("pieChart2").innerHTML = "";
    var svg = d3.select('#pieChart2')
      .append('svg')
      .attr('width', width)
      .attr('height', height)
      .append('g')
      .attr('transform', 'translate(' + (width / 2) +
        ',' + (height / 2) + ')');
    var arc: any;
    arc = d3.arc()
      .innerRadius(radius - donutWidth)
      .outerRadius(radius);

    var pie = d3.pie()
      .value(function (d: any) { return d.count; })
      .sort(null);

    svg.selectAll('path')
      .data(pie(dataset))
      .enter()
      .append('path')
      .attr('d', arc)
      .attr('fill', function (d: any, i) {
        return color(d.data.label);
      });


    var legend = svg.selectAll('.legend')                     // NEW
      .data(dataset)                                   // NEW
      .enter()                                                // NEW
      .append('g')                                            // NEW
      .attr('class', 'legend')                                // NEW
      .attr('transform', function (d, i) {                     // NEW
        var height = legendRectSize + legendSpacing;          // NEW
        var offset = height * color.domain().length / 2;     // NEW
        var horz = -2 * legendRectSize - 55;                       // NEW
        var vert = i * height - offset;                       // NEW
        return 'translate(' + horz + ',' + vert + ')';        // NEW
      });                                                     // NEW

    legend.append('rect')                                     // NEW
      .attr('width', legendRectSize)                          // NEW
      .attr('height', legendRectSize)                         // NEW
      .style('fill', function (d: any) {
        return color(d.label)
      })                                   // NEW
      .style('stroke', 'black');                                // NEW

    legend.append('text')                                     // NEW
      .attr('x', legendRectSize + legendSpacing)              // NEW
      .attr('y', legendRectSize - legendSpacing)              // NEW
      .text( (d: any) => {
         let count = this._decimalPipe.transform(d.count)
         return d.label + '(' + count + ')' + (d.presentage ? '(' + d.presentage + '%)' : '') 
        });
  }

}



