import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { API_HOST } from '../../app/constants';

/*
  Generated class for the QueryExecutorProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class QueryExecutorProvider {

  constructor(public http: HttpClient) {
    console.log('Hello QueryExecutorProvider Provider');
  }
  public executeQuery(params:any):Observable<any>{
    return this.http.post(API_HOST+'/api/mobile/getdata',params);
  }
  public execute(params:any):Observable<any>{
    return this.http.post(API_HOST+'/api/mobile/getdata/'+ params.code,params);
  }
  public readFile(path):Observable<any>{
    return this.http.get(path);
  }
  public executeMultipleQuery(fieldKeys: Array<any>): Array<{ code: string, parameters: any }> {
    let queries = [], sequence = 1;
    fieldKeys.forEach(fieldKey => {
      queries.push({
        code: fieldKey,
        sequence: sequence
      });
      sequence += 1;
    });
    return queries;
  }
  public runQueries(requestDtos: Array<{ code: string, parameters: any, sequence: number }>): Observable<any> {
    return this.http.post(API_HOST + '/api/resource/getdatas', requestDtos);
  }
  public runAllQueries(requestDtos: Array<{ code: string, parameters: any, sequence: number }>): Observable<any> {
    return this.http.post(API_HOST + '/api/resource/getalldata', requestDtos);
  }
}



