let ENV_DATA: Object;
declare var process: {
  env: {
    BUILD_ENV: string
  }
}
switch (process.env.BUILD_ENV) {
  case 'staging':
    ENV_DATA = {
      apiUrl: 'http://imtstaging.argusoft.com:8181',
      APP_NAME: 'State of Health'
    }
    break;
  case 'prod':
    ENV_DATA = {
      apiUrl: 'https://techo.gujarat.gov.in',
      APP_NAME: 'State of Health'
    }
    break;
  case 'erase':
    ENV_DATA = {
      apiUrl: 'http://ng.argusoft.com/',
      APP_NAME: 'State of Health'
    }
    break;
  case 'nigeria':
    ENV_DATA = {
      apiUrl: 'http://covidresponseng.com',
      APP_NAME: 'State of Health'
    }
    break;
  default:
    ENV_DATA = {
      apiUrl: 'https://techo.gujarat.gov.in',
      APP_NAME: 'Erase'
    }
}

export const ENV_API_HOST = ENV_DATA;



