import { Component } from '@angular/core';
import { SohUserServiceProvider } from '../../providers/soh-user-service/soh-user-service';
import { AlertController, NavController, LoadingController } from 'ionic-angular';
import { UtilityHelperProvider } from '../../providers/utility-helper/utility-helper';
import { TranslateService } from '@ngx-translate/core';

/**
 * Generated class for the RegisterFormComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'register-form',
  templateUrl: 'register-form.html'
})
export class RegisterFormComponent {

  name: string;
  designation: string;
  organization: string;
  purpose: string;
  mobileNo: string;
  otp:string;
  showOPT:boolean=false;
  user:any;
  isRegistered: boolean = false;
  constructor(
    private alterController: AlertController,
    private navCtrl: NavController,
    private sohUserService: SohUserServiceProvider,
    private utilHelper: UtilityHelperProvider,
    private translate: TranslateService,
    private loading: LoadingController) {
    this.onInit();
  }
  onInit() {
  }
  requestAccess(){
    //let user = this.getUser();
    this.user.otp = this.otp;
    let loading = this.loading.create();

    this.sohUserService.sohRegister(this.user).subscribe(user => {
      let me = this;
      loading.dismiss();
      let successAlter = this.alterController.create({
        title: this.translate.instant('RegisterSuccess'),
        message: this.translate.instant('SuccessRegistration'),
        buttons: [{
          text: this.translate.instant('MaintenanceButtonText'),
          handler: function () {
            me.navCtrl.pop();
          }
        }]
      })
      successAlter.present();
    }, (failed) => {
      if (failed.error) {
        this.utilHelper.showToast(failed.error.message);
      } else {
        this.utilHelper.showToast(this.translate.instant('ProblemWithReg'));
      }

      loading.dismiss();
    })
  }
  onBack(){
    this.showOPT=false;
  }
  getUser(){
    let user: any = {};
    user.name = this.name;
    user.designation = this.designation;
    user.organization = this.organization;
    user.purpose = this.purpose;
    user.mobileNo = this.mobileNo;
    return user;
  }
  sendOTP() {
    
    if (this.utilHelper.isBlank(this.name) || this.utilHelper.isBlank(this.designation)
      || this.utilHelper.isBlank(this.purpose) || this.utilHelper.isBlank(this.mobileNo)) {
      this.utilHelper.showToast(this.translate.instant('FillAllFields'));
      return;
    }
    if (this.mobileNo.toString().length != 10) {
      this.utilHelper.showToast(this.translate.instant('InvalidMobileNo'));
      return;
    }
    
    let loading = this.loading.create();
    loading.present();
    let user = this.getUser();
    this.sohUserService.sohRegisterSendOTP(user).subscribe(user => {
      loading.dismiss();
      this.user = user;
      this.showOPT=true;
    }, (err) => {
      if (err.error) {
        this.utilHelper.showToast(err.error.message);
      } else {
        this.utilHelper.showToast(this.translate.instant('ProblemWithReg'));
      }

      loading.dismiss();
    })
  }
}



