import { Component } from '@angular/core';
import { 
  NavController } from 'ionic-angular';
import { StateOfHealthComponent } from '../../components/state-of-health/state-of-health';
import { ROOT_LOCATION } from '../../app/constants';
import { TranslateService } from '@ngx-translate/core';

/**
 * Generated class for the HomePage tabs.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  public rootLocation = ROOT_LOCATION;
  public myLocation = this.translateService.instant("MyLocation")
  tabData = {
    locationId: this.rootLocation.LOCATION_ID,
    drillingEnable:false
  };
  gujaratLocationRoot = StateOfHealthComponent
  myLocationRoot = StateOfHealthComponent
  constructor(public navCtrl: NavController,private translateService:TranslateService) {

  }

}



