import { Pipe, PipeTransform } from '@angular/core';
import {DecimalPipe} from '@angular/common';
/**
 * Generated class for the NumberStringPipe pipe.
 *
 * See https://angular.io/api/core/Pipe for more info on Angular Pipes.
 */
@Pipe({
  name: 'numberString',
})
export class NumberStringPipe implements PipeTransform {
  /**
   * Takes a value and makes it lowercase.
   */
  transform(value) {
      if(!value || value == null){
        return value;
      }      
      let tmpVal = parseFloat(value.toString());
      if(isNaN(tmpVal)){
        return value;
      }
      let pipe:DecimalPipe = new DecimalPipe('en-US');
      return pipe.transform(tmpVal);
  }
}



