import { Injectable } from "@angular/core";
import { LOCAL_STORAGE } from "../../app/constants";
import { LocalStorageProvider } from "../local-storage/local-storage";
import { InMemoryStorageProvider } from "../in-memory-storage/in-memory-storage";

@Injectable()
export class LocaleService {
    private localeObject = {
        localeVersion: 0,
        labels: {}
    }


    constructor(public localStorage: LocalStorageProvider,
        public inMemoryStorage: InMemoryStorageProvider) {
        this.localStorage.get(LOCAL_STORAGE.LOCALE).then(locale => {
            if (locale) {
                this.inMemoryStorage.put(LOCAL_STORAGE.LOCALE, locale);
            } else {
                this.inMemoryStorage.put(LOCAL_STORAGE.LOCALE, this.localeObject);
            }
        })
    }

    public set(labels, localeVersion) {
        this.localeObject.localeVersion = localeVersion;
        this.localeObject.labels = labels;
        this.inMemoryStorage.put(LOCAL_STORAGE.LOCALE, this.localeObject);
        return this.localStorage.set(LOCAL_STORAGE.LOCALE, this.localeObject);
    }

    public get() {
        return this.inMemoryStorage.get(LOCAL_STORAGE.LOCALE);
    }

}


