import { Component } from '@angular/core';
import { QueryExecutorProvider } from '../../providers/query-executor/query-executor';
import { NavParams, LoadingController } from 'ionic-angular';
import { CallNumber } from '@ionic-native/call-number';
import { UtilityHelperProvider } from '../../providers/utility-helper/utility-helper';
import { StateOfHealthServiceProvider } from '../../providers/state-of-health-service/state-of-health-service';
import { LINELIST_TYPE,LIMIT } from '../../app/constants';

/**
 * Generated class for the MemberLineListComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'member-line-list',
  templateUrl: 'member-line-list.html'
})
export class MemberLineListComponent {

  lineList: any = [];
  elementName: any;
  locationId: any;
  element: any;
  selectedDateFilter:any
  elementConfig:any;
  fieldConfig:any;
  keys:any =[];
  type:any;
  lineListLabel:any;
  offset:number = 0;
  limit: any;
  showMoreButton: boolean = true;
  searchTxt:string= null;
  isSearchingEnable: boolean = false;
  constructor(private queryExecutor: QueryExecutorProvider,
    private loading: LoadingController,
    private callNumber: CallNumber,
    private navParam: NavParams,
    private utilHelper : UtilityHelperProvider,
    private stateOfHealthService: StateOfHealthServiceProvider) {
    this.elementName = this.navParam.get('elementName');
    this.locationId = this.navParam.get('locationId');
    this.element = this.navParam.get('element');
    this.selectedDateFilter = this.navParam.get('selectedDateFilter');
    this.elementConfig = this.navParam.get('selectedElementConfig');
    this.fieldConfig = this.navParam.get('fieldConfig');
    this.type = this.navParam.get('type');
    this.init();
  }
  onShowMore(member,element){
    this.utilHelper.onShowMoreForMemberList(member,element,this.elementConfig)
  }
  init() {
    let queryCode;
    if(this.type==LINELIST_TYPE.ELEMENT){
      queryCode = this.elementConfig.lineList.query;
      this.lineListLabel = this.elementConfig.lineList.elementLineListLable;
      this.isSearchingEnable = this.elementConfig.lineList.isSearchingEnable;
      this.limit = this.elementConfig.lineList.paginationEnable && this.elementConfig.lineList.limit ? this.elementConfig.lineList.limit : LIMIT;
    }else if(this.type==LINELIST_TYPE.FIELD){
      queryCode = this.fieldConfig.query;
      this.lineListLabel = this.fieldConfig.name;
      this.isSearchingEnable = this.fieldConfig.isSearchingEnable;
      this.limit = this.fieldConfig.paginationEnable && this.fieldConfig.limit ? this.fieldConfig.limit : LIMIT;
    } 
    let dateFilter = this.selectedDateFilter;
    let betweenDates;
    if(this.elementName == "FI"){
       betweenDates = this.utilHelper.getBetweenDays(dateFilter.value);
    }else{
       betweenDates = this.utilHelper.getBetweenDates(dateFilter.value);
    }
    let params;
    if((this.type==LINELIST_TYPE.ELEMENT && this.elementConfig.lineList.paginationEnable) ||
      (this.type==LINELIST_TYPE.FIELD && this.fieldConfig.paginationEnable)){
      params = {
        "code": queryCode,
        "parameters": { "location_id": this.locationId, limit: this.limit, fromDate: betweenDates[0],toDate: betweenDates[1] , offset : this.offset,search_text:this.searchTxt==''? null : this.searchTxt}
      };
    } else {
      params = {
        "code": queryCode,
        "parameters": { "location_id": this.locationId, limit: 10000, fromDate: betweenDates[0],toDate: betweenDates[1],search_text:this.searchTxt==''? null : this.searchTxt }
      };
      this.showMoreButton = false;
    }
    let making = this.loading.create();
    making.present();
    this.queryExecutor.executeQuery(params).subscribe(response => {
      if(response.result.length < this.limit){
        this.showMoreButton = false;
      }
      this.lineList = this.lineList.concat(response.result);
      if(this.lineList.length>0){
      this.keys = Object.keys(this.lineList[0]);
      }
      this.offset += this.limit
      making.dismiss();
    }, (err) => {
      making.dismiss();
      this.utilHelper.onApiFailure(err);
    })
  }
  onClearTest(){
    this.onResetParams();
    this.searchTxt=null;
    this.init();
  }
  onResetParams(){
    this.offset=0;
    this.lineList=[];
    this.showMoreButton=true;
  }
  onCall(no:string){
    this.callNumber.callNumber(no, false)
    .then(res => console.log('Launched dialer!', res))
    .catch(err => console.log('Error launching dialer', err));
  }
  onSearch(){
    this.onResetParams();
    this.init();
  }
}



