import { Component, Input } from '@angular/core';
import { AlertController, ToastController } from 'ionic-angular';
import { TranslateService } from '@ngx-translate/core';

/**
 * Generated class for the HelpComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'help',
  templateUrl: 'help.html'
})
export class HelpComponent {

  @Input('message')message: string;
  @Input('messageTitle')messageTitle: string;
  @Input('title')title: string;
  @Input('customcls')customClsInput:string='fix-width-col'
  customCls:string= 'fix-width-col';
  constructor(public alertCtrl : AlertController, public toastCtrl: ToastController,
    public translate: TranslateService) {
    
  }
  ngOnInit(){
    if(this.customClsInput){
      this.customCls = this.customClsInput;
    }
  }  
   onShowHelp(){
     let alert = this.alertCtrl.create({
       title: this.messageTitle,
       message: this.message,
       cssClass: 'tt-alert-box',
       buttons: [
         {
           text: this.translate.instant('Close')
         }
       ]
     });
     alert.present();
   }

  /* onShowHelp(){
    let toast = this.toastCtrl.create({
      message: this.message,
      position: 'bottom',
      showCloseButton: true,
      closeButtonText: 'OK',
      dismissOnPageChange: true,
      cssClass: 'soh-toast'
    });
    toast.present();
  }*/

}



