import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage'
import { InMemoryStorageProvider } from '../in-memory-storage/in-memory-storage';
import { LOCAL_STORAGE, ROOT_LOCATION } from '../../app/constants';
/*
  Generated class for the LocalStorageProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class LocalStorageProvider {

  constructor(public storage:Storage, public inMemoryStorage:InMemoryStorageProvider) {
    console.log('Hello LocalStorageProvider Provider');
  }
  public set(key, value) {
    this.inMemoryStorage.put(key,value);
    return this.storage.set(key, value);
  }
  public get(key) {
    return this.storage.get(key);
  }
  public remove(key){
    this.inMemoryStorage.remove(key);
    return this.storage.remove(key);
  }
  public setRegisterUser(user:any){
    user.minLocationId = ROOT_LOCATION.LOCATION_ID;
    this.set(LOCAL_STORAGE.LOGIN_USER,user);
    this.set(LOCAL_STORAGE.IS_ANONYMOUS_LOGIN,false);
  }
}



