import { Component, Input, ViewChild } from '@angular/core';
import { PieChartComponent } from '../pie-chart/pie-chart';
import { UtilityHelperProvider } from '../../providers/utility-helper/utility-helper';
import { NavController, AlertController } from 'ionic-angular';
import { InMemoryStorageProvider } from '../../providers/in-memory-storage/in-memory-storage';
import { StateOfHealthDetailsComponent } from '../state-of-health-details/state-of-health-details';
import { MemberLineListComponent } from '../member-line-list/member-line-list';
import { LOCAL_STORAGE, CORPORATION, NAME_MAPPING_MAP, STAT_DISTRICT, LINELIST_TYPE, DEFAULT_MAP_COLOR } from '../../app/constants';
import { LoginComponent } from '../login/login';
import * as d3 from 'd3';
import * as topojson from "topojson-client";
import { QueryExecutorProvider } from '../../providers/query-executor/query-executor';
import { LineChartComponent } from '../line-chart/line-chart';
import { MapViewComponent } from '../map-view/map-view';
import { StatusReportComponent } from '../status-report/status-report';
import { Covid19ResultsComponent } from '../covid19-results/covid19-results';
import { TranslateService } from '@ngx-translate/core';
import { StateOfHealthMapComponent } from '../state-of-health-map/state-of-health-map';
/**
 * Generated class for the ElementDetailsComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'element-details',
  templateUrl: 'element-details.html'
})
export class ElementDetailsComponent {

  @Input('selectedelementConfig') elementConfig:any;
  @Input('selectedElement') selectedElement:any;
  @Input('stateData') stateData:any;
  @Input('selectedDateFilter') selectedDateFilter:any;
  @Input('drillingEnable') drillingEnable:any;
  @Input('locationLevel') locationLevel:any;
  @Input('tabData') selectedElementConfig:any;
  @Input('timeLineLabel') timeLineLabel:any;
  @Input('heading1') heading1:any;
  @Input('heading2') heading2:any;
  @Input('totalHeading') totalHeading:any;
  @Input('randomMapId') randomMapId: any;
  @ViewChild(PieChartComponent) piechart: PieChartComponent;
  @ViewChild(StateOfHealthMapComponent) map: StateOfHealthMapComponent;
  mapCode: any = 'polygons';
  mapPropertyName: any = 'district';
  mapFound: boolean = false;
  constructor(public utilityHelper: UtilityHelperProvider,
    public navCtrl: NavController,
    public inMemoryStorage: InMemoryStorageProvider,
    public alterController: AlertController,
    public translate: TranslateService,
    private queryExecutor: QueryExecutorProvider) {
  }
  reloadPieChart(selectedElement,selectedElementConfig){
    this.piechart.reloadData(selectedElement,selectedElementConfig || this.selectedElementConfig);
  }
  public isLogin() {
    return this.utilityHelper.isLogin();
  }
  public isDisplayFlag() {
    if (this.selectedElement.type == 'S' || this.selectedElement.type == 'D' || this.selectedElement.type == 'C' || this.selectedElement.type == 'B' || this.selectedElement.type == 'Z') {
      if(this.elementConfig.enableReporting)
      {
        return true;
      }else{
        return false;
      }
    } else {
      return false;
    }
  }
  isClickable(element){
    let n;
    if(this.isLogin()){
    n = this.utilityHelper.isClickable(element,this.selectedElementConfig,this.drillingEnable);
    }else{
    n = this.utilityHelper.isClickable(element,this.selectedElementConfig,false);
    }
    if(n==1 || n==3){
      return false;
    }else{
      return true;
    }
  }
  onSelectLocation(element) {
    let n=this.utilityHelper.isClickable(element,this.selectedElementConfig,this.drillingEnable)
    if(n==1 || n==3){
      return;
    }else if(n==2){
      this.getLineList(element);
    }
    else {
      this.navCtrl.push(StateOfHealthDetailsComponent, { element: element, elements: '', selectedElements: '', locationLevel: this.selectedElement.level, drillingEnable: this.drillingEnable, dateFilter: this.selectedDateFilter, selectedElementConfig:this.elementConfig, tabName:this.selectedElementConfig.tabName });
    }
  }
  isLineListShow(element) {
    if (element.type != 'S' && element.type != 'D' && element.type != 'C') {
      return true;
    } else {
      return false;
    }
  }
  getLineList(element) {
    if (this.inMemoryStorage.get(LOCAL_STORAGE.LOGIN_USER)) {
      if (this.isLineListShow(element)) {
        this.navCtrl.push(MemberLineListComponent,
          { elementName: this.selectedElement.elementName, locationId: element.locationId, element: element, selectedDateFilter:this.selectedDateFilter,selectedElementConfig:this.selectedElementConfig, type:LINELIST_TYPE.ELEMENT});
      }
    } else {
      let alert = this.alterController.create({
        title: this.translate.instant('LoginRequired'),
        message: this.translate.instant('LoginRequiredDescription'),
        buttons: [{
          text: this.translate.instant('Cancel'),
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
          }
        }, {
          text: this.translate.instant('GotoLogin'),
          handler: () => {
            this.inMemoryStorage.put(LOCAL_STORAGE.AREA_ELEMENT, element)
            this.inMemoryStorage.put(LOCAL_STORAGE.CORRENT_DATE_FILTER,this.selectedDateFilter);
            this.inMemoryStorage.put(LOCAL_STORAGE.ELEMENT_CONFIG,this.selectedElementConfig);
            this.navCtrl.push(LoginComponent, { isBackButtonEnable: true });
          }
        }]
      });
      alert.present();

    }
  }
  onShowDetails(element){
    if(element.isShow=="undefined"){ 
    element.isShow=true;
    }else{
      element.isShow=!element.isShow;
    }
  }
  loadMap(selectedElementConfig) {
    //For svg support
    setTimeout(()=>{
      if (this.selectedElement.currentLevel >= selectedElementConfig.minLocationLevel && this.selectedElement.currentLevel <= selectedElementConfig.maxLocationLevel) {
        let tag = document.getElementById(`footerDescription${this.selectedElement.currentLevel}`);
        if (tag) {
          tag.innerHTML = selectedElementConfig.footerDescription || null;
        }
      }
    },1000)
    
    if (this.map!=null) {
      this.map.reloadData(this.stateData, this.selectedElement, this.locationLevel,selectedElementConfig);
    }
    
  }
  showLineChart(){
    this.navCtrl.push(LineChartComponent,{
      selectedElementConfig: this.selectedElementConfig,
      selectedElement: this.selectedElement,
      elementConfig: this.elementConfig
    });
  }
  showStatusReport(){
    this.navCtrl.push(StatusReportComponent, {
      selectedElementConfig: this.selectedElementConfig
    })
  }
  covid19Details(){
    this.navCtrl.push(Covid19ResultsComponent);
  }
}



