import { Pipe, PipeTransform } from '@angular/core';
import { DecimalPipe } from '@angular/common';
/**
 * Generated class for the FormatPipe pipe.
 *
 * See https://angular.io/api/core/Pipe for more info on Angular Pipes.
 */
@Pipe({
    name: 'format',
})
export class FormatPipe implements PipeTransform {
    /**
     * Takes a value and format accordingly. Here input ${variable} replace by it's value 
     */
    transform(input: string, value: any) {
        let matchExpression = input.match(/\${([^}]*)}/g);
        if(matchExpression && matchExpression.length > 0){
            matchExpression.forEach(element => {
                element = element.replace("${","").replace("}","");
                if(value[element] !== undefined){
                    input = input.replace("${"+element+"}",value[element])
                }
            });
        }
        return input;
    }
}



