import { Component } from "@angular/core";
import { QueryExecutorProvider } from "../../providers/query-executor/query-executor";
import { forkJoin } from "rxjs/observable/forkJoin";
import { NavParams, LoadingController } from "ionic-angular";
import { UtilityHelperProvider } from "../../providers/utility-helper/utility-helper";

@Component({
    selector: 'status-report',
    templateUrl: 'status-report.html'
})
export class StatusReportComponent {

    public positiveMemberList: any = [];
    public deathMemberList: any = [];
    public markerDetails: any = [];
    public recoveredDetails: any = [];
    public selectedElementConfig: any;
    public lastMarkerDate: any;
    public showOtherColumns: boolean = false;
    public sortedFieldNameForPositive: any;
    public sortedFieldNameForDeath: any;
    public sortedFieldNameForRecover: any;
    public sortingForPositiveNew = {
        isNameSortAsc: true,
        isGenderSortAsc: true,
        isFacilitySortAsc: true
    }
    public sortingForDeathsNew = {
        isNameSortAsc: true,
        isGenderSortAsc: true,
        isFacilitySortAsc: true
    }
    public sortingForRecover = {
        isNameSortAsc: true,
        isGenderSortAsc: true,
        isFacilitySortAsc: true
    }
    constructor(private queryExecutor: QueryExecutorProvider,
        public navParams: NavParams,
        private utilityHelper: UtilityHelperProvider,
        private loading: LoadingController
    ) {
    }

    ionViewDidLoad() {
        this.initializePage()
    }

    initializePage() {
        this.getPositiveData();
        this.getDeathData();
        this.getMarkerData();
        this.retriveLastMarkerDate()
        this.getRecoveredDetails();
    }
    retriveLastMarkerDate() {
        let making = this.loading.create();
        making.present();
        let query = {
            code: 'retrieve_system_configuration_by_key',
            parameters: {
                key: 'COVID19_LAST_MARKET_DATE'
            }
        }
        this.queryExecutor.execute(query).subscribe(res=>{
            this.lastMarkerDate = res.result[0] && res.result[0].value;
            making.dismiss();
        },err=>{
            making.dismiss();
            this.utilityHelper.onApiFailure(err);
        })
    }
    getRecoveredDetails(){
        let making = this.loading.create();
        making.present();
        let query = {
            code: 'soh_covid19_recover_member_list',
        }
        this.queryExecutor.execute(query).subscribe(res => {
            this.recoveredDetails = res.result;
            this.recoveredDetails.forEach(element => {
                element.showOtherDetails = false
            });
            this.sortDetailsForRecover('name',true);
            this.sortingForRecover.isNameSortAsc = false;
            making.dismiss();
        }, err => {
            console.log(err);
            making.dismiss();
        })
    }
    getPositiveData() {
        let making = this.loading.create();
        making.present();
        let query = {
            code: 'soh_covid19_positive_member_list',
        }
        this.queryExecutor.execute(query).subscribe(res => {
            this.positiveMemberList = res.result;
            this.positiveMemberList.forEach(element => {
                element.showOtherDetails = false
            });
            this.sortDetails('name',true);
            this.sortingForPositiveNew.isNameSortAsc = false;
            making.dismiss();
        }, err => {
            console.log(err);
            making.dismiss();
        })
    }

    getDeathData() {
        let making = this.loading.create();
        making.present();
        let query = {
            code: 'soh_covid19_death_member_list',
        }
        this.queryExecutor.execute(query).subscribe(res => {
            this.deathMemberList = res.result;
            this.deathMemberList.forEach(element => {
                element.showOtherDetails = false
            });
            this.sortDetailsForDeaths('name',true);
            this.sortingForDeathsNew.isNameSortAsc = false;
            making.dismiss();
        }, err => {
            making.dismiss();
            console.log(err)
        })
    }

    getMarkerData() {
        let making = this.loading.create();
        making.present();
        let query = {
            code: 'soh_covid19_marker_data',
        }
        this.queryExecutor.executeQuery(query).subscribe(res => {
            if(res.result && res.result[0]){
                this.markerDetails = res.result[0];
            }
            making.dismiss();
        }, err => {
            making.dismiss();
            console.log(err);
        })
    }

    sortDetails(field, sortType) {
        this.sortedFieldNameForPositive = field;
        this.sortData(field, sortType)
    }

    sortData(field, sortType) {
        this.utilityHelper.sortAllTypeOfDetailsByFieldAndType(this.positiveMemberList,field,sortType);
    }

    sortDetailsForDeaths(field, sortType) {
        this.sortedFieldNameForDeath = field;
        this.sortDataForDeaths(field, sortType)
    }

    sortDataForDeaths(field, sortType) {
        this.utilityHelper.sortAllTypeOfDetailsByFieldAndType(this.deathMemberList,field,sortType);

    }

    sortDetailsForRecover(field, sortType) {
        this.sortedFieldNameForRecover = field;
        this.sortDataForRecover(field, sortType)
    }

    sortDataForRecover(field, sortType) {
       this.utilityHelper.sortAllTypeOfDetailsByFieldAndType(this.recoveredDetails,field,sortType);
    }

}



