import { Component, ViewChild } from '@angular/core';
import { Chart } from 'chart.js';
import { StateOfHealthServiceProvider } from '../../providers/state-of-health-service/state-of-health-service';
import { NavParams, LoadingController, Slides, ToastController } from 'ionic-angular';
import { LOCAL_STORAGE, ROOT_LOCATION } from '../../app/constants';
import { InMemoryStorageProvider } from '../../providers/in-memory-storage/in-memory-storage';
import { QueryExecutorProvider } from '../../providers/query-executor/query-executor';
import { UtilityHelperProvider } from '../../providers/utility-helper/utility-helper';


/**
 * Generated class for the LineChartComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'line-chart',
  templateUrl: 'line-chart.html'
})
export class LineChartComponent {
  chartConfig: any = [];
  public selectedElementConfig: any;
  elementsConfig: any;
  timelineData: any;
  selectedElement: any;
  elementConfig: any;
  isAnyTabTimelineEnable: boolean = false
  ratioData : any = [];
  ratioKeys = []
  ratioDetails : any;
  activeCasesDetails: any = [];
  daysOptions = [];
  selectedNoOfDays= 24;
  selectedNoOfToDays = 30
  public sortingForRecover = {
    isNameSortAsc: true,
    isGenderSortAsc: true,
    isFacilitySortAsc: true
  }
  sortedFieldNameForRecover: any;
  @ViewChild(Slides) slides: Slides;
  slideOpts = {
    initialSlide: 1,
    speed: 400
  };
  rootLocation = ROOT_LOCATION;

  constructor(private stateOfHealthService: StateOfHealthServiceProvider,
    public inMemoryStorage: InMemoryStorageProvider,
    public toastController: ToastController,
    private queryExecutor: QueryExecutorProvider,
    private loading: LoadingController,
    public utils: UtilityHelperProvider,
    public navParams: NavParams) {
    this.selectedElementConfig = navParams.get('selectedElementConfig');
    this.selectedElement = navParams.get('selectedElement');
    this.elementConfig = navParams.get('elementConfig');
    this.elementsConfig = this.stateOfHealthService.getElementConfig();
  }

  ionViewDidLoad() {
    if(this.selectedElementConfig.isAnalyticsPanelEnable){
      this.getRatioData();
    }
    this.getActiveCases();
    this.loadTimeLineData();
    this.getChartData();
    this.setSlectDaysOptions();
  }
  setSlectDaysOptions(){
    for(let i=0;i<30;i++){
      this.daysOptions.push({
        name : (i+1).toString(),
        value: i+1
      });
    }
    
  }
  async getActiveCases() {
    if(parseInt(this.selectedNoOfDays.toString()) == 31){
      this.selectedNoOfToDays = 1000;
    }
    if(parseInt(this.selectedNoOfDays.toString()) != 31 && parseInt(this.selectedNoOfToDays.toString()) == 1000){
      this.selectedNoOfToDays = 30;
    }
    if(parseInt(this.selectedNoOfToDays.toString())<parseInt(this.selectedNoOfDays.toString())){
      const toast = this.toastController.create({ message: 'Please select valid days', duration: 2000 }); 
      toast.present();
      return;
    }

    let query = {
      code: 'covid19_fetch_patients_admitted_more_than_15_days',
      parameters:{
        days:Number(this.selectedNoOfDays),
        to_days:this.selectedNoOfToDays
      }
    }
    this.queryExecutor.execute(query).subscribe(res => {
      this.activeCasesDetails = res.result;
      this.activeCasesDetails.forEach(element => {
        element.showOtherDetails = false;
      });
      this.sortDetails('name',true);
      this.sortingForRecover.isNameSortAsc = false;
    }, err => {
      console.log(err)
    })
  }
  getRatioData(){
    let queries = this.selectedElementConfig.analyticsPanels.map(x => x.query);
    let requestDtos: Array<any> = this.queryExecutor.executeMultipleQuery(queries)
    this.queryExecutor.runAllQueries(requestDtos).subscribe(res =>{
      this.selectedElementConfig.analyticsPanels.forEach((element,i) => {
        element.result = res[i].result[0];
        element.keys = Object.keys(element.result)
      });
    },err=>{
      console.log(err)
    })
  }
  getChartData() {
    let query = {
      code: 'soh_performance_line_chart',
      parameters: {
        locationId: this.selectedElement.locationId
      }
    }
    this.queryExecutor.execute(query).subscribe(res => {
      for (let chart of res.result) {
        let chartData = {
          'id': chart.id,
          'name': chart.display_name,
          'module': chart.module,
          'type': chart.chartType === 'BAR_STATE' ? 'bar' : 'line',
          'chartConfig': JSON.parse(chart['data'])
        }
        // chart = JSON.parse(chart['data'])
        this.chartConfig.push(chartData)
      }
      console.log(this.chartConfig)
    }, err => {
      console.log(err);
      this.utils.onApiFailure(err);
    }).add(() => {
      setTimeout(() => {
        this.lineChartMethod();
      }, 1000)
    })
  }

  loadTimeLineData() {
      let making = this.loading.create();
      making.present();
      this.queryExecutor.execute({ "code": "state_of_health_get_timeline_data_1", "parameters": { "locationId": this.selectedElement.locationId } }
      ).subscribe((data) => {
        this.timelineData = data.result;
        for (let elementConfig of this.elementsConfig) {
          for (let elements of elementConfig.elements) {
            for (let tab of elements.tabs) {
              if (tab.isTimelineEnable) {
                tab.selectedChart = tab.fieldName;
                this.showChart(tab);
                this.isAnyTabTimelineEnable = true;
              } else {
                this.isAnyTabTimelineEnable = false;
              }
            }
          }
        }
        making.dismiss();
      }, (error) => {
        making.dismiss();
      });
  }

  lineChartMethod() {
    console.log(this.chartConfig)
    for (let chart of this.chartConfig) {
      let chartData = chart.chartConfig;
      if (this.elementConfig.module == chart.module) {
        if (chartData.length) {
          let json = []
          for (let jsonData of chartData) {
            let data = jsonData.json.find(x => x.axis === 'y');
            let other = jsonData.json.find(x => x.axis === 'other');
            if (data) {
              json.push(data)
            }
            if (other) {
              json.push(other)
            }
          }
          json.push(chartData[0].json.find(x => x.axis === 'x'))
          chartData.json = json;
          this.generateChart(chartData, chart.id, chart.type)
        } else {
          this.generateChart(chartData, chart.id,chart.type)
        }
      }
    }
  }

  showChart(tab) {
    if (tab.isTimelineEnable && document.getElementById(`${tab.tabName}`)) {
      this.isAnyTabTimelineEnable = true;
      var chartValues = []
      tab.fields.forEach(x => {
        if (x.isTimelineEnable && x.fieldName == tab.selectedChart) {
          let value = this.timelineData.map((timeLineData) => {
            if (timeLineData.element_name === tab.elementName) {
              return {
                'value': timeLineData[x.fieldName] || "0",
                'timeline': timeLineData.timeline
              }
            }
          }).filter(timeLineData => {
            if (timeLineData) {
              return timeLineData
            } else {
              return null
            }
          })
          chartValues.push(value)
        }
      });

      let canvas = document.getElementById(`${tab.tabName}`);
      let options = {
        layout: {
          padding: {
            top: 5
          }
        },
        legend: {
          display: false
        },
        scales: {
          xAxes: [{
            ticks: {
              display: true,
              callback: function (label, index, labels) {
                if (index == 0 || labels.length - 1 == index) {
                  return label;
                }
              }
              //this will remove only the label
            },
            gridLines: {
              color: "rgba(0, 0, 0, 0)",
              display: false,
              drawBorder: false
            }
          }],
          yAxes: [{
            ticks: {
              display: false //this will remove only the label
            },
            gridLines: {
              color: "rgba(0, 0, 0, 0)",
              display: false,
              drawBorder: false
            }
          }]
        }
      }
      let labels = [];
      let data = [];
      if (chartValues[0]) {
        labels = chartValues[0].map(x => x.timeline).reverse();
        data = chartValues[0].map(x => x.value).reverse();
      }
      if (chartValues.length > 0) {
        let chartData = {
          type: 'line',
          data: {
            labels: labels,
            datasets: [{
              data: data,
              borderWidth: 2,
              fill: false,
              borderColor: "blue",
              pointBackgroundColor: "blue"
            }]
          },
          options: options
        }
        new Chart(canvas, chartData);
      }
    } else {
      this.isAnyTabTimelineEnable = false;
    }
  }

  generateChart(chart, id,type) {
    let canvas = document.getElementById(id);
    let datasets = [];
    let datasetValue = chart.json.filter(x => x.axis === 'y');
    let labels = chart.json.filter(x => x.axis === 'x');
    let others = chart.json.filter(x => x.axis === 'other');
    for (let data of datasetValue) {
      let dataset = {
        label: data.display_name,
        fill: false,
        backgroundColor: type === 'bar' ? data.color : 'transparent',
        borderColor: data.color,
        borderWidth: 1.5,
        pointBorderColor: 'black',
        pointBackgroundColor: data.color,
        data: data.value
      }
      datasets.push(dataset);
    }

    let options = {
      tooltips: {
        callbacks: {
          label: (tooltipItem, data) => {
            let label = data.datasets[tooltipItem.datasetIndex].label + ': ';
            label +=  this.utils.formatNumber(tooltipItem.yLabel,true) 
            return label; 
          },
          afterLabel: function (tooltipItems, data) {
            let field = data.datasets[tooltipItems.datasetIndex];
            let otherData = '';
            for (let other of others) {
              if (other.name === field.label || other.display_name === field.label) {
                if (other.value[tooltipItems.index]) {
                  otherData += (other.axisName + ':' + other.value[tooltipItems.index]) + ' '
                }
              }
            }
            return otherData ? otherData : ''
          }
        }
      },
      scales: {
        yAxes: [{
          ticks: {
            display: true,
            callback: (label, index, labels) => {
              return this.utils.formatNumber(label,false)
            }
          }
        }]
      }
    }

    if (type === 'bar') {
      let stackOption = {
        "scales": {
          xAxes: [{
            stacked: true // this should be set to make the bars stacked
          }],
          yAxes: [{
            stacked: true, // this also..
            callback: (label, index, labels) => {
              // In order to fomat number like 1000 -> 1k
              return this.utils.formatNumber(label,false)
            }
          }]
        }
      }
      options = Object.assign(options, stackOption)
    }

    let chartData = {
      type: type,
      data: {
        labels: labels[0].value,
        datasets: datasets
      },
      options: options
    }
    console.log(chartData)
    new Chart(canvas, chartData);
  }

  sortDetails(field, sortType) {
    this.sortedFieldNameForRecover = field;
    this.utils.sortAllTypeOfDetailsByFieldAndType(this.activeCasesDetails,field, sortType)
}

}



