import { Component } from '@angular/core';
import { QueryExecutorProvider } from '../../providers/query-executor/query-executor';
import { LoadingController, NavController } from 'ionic-angular';
import { MemberHealthInfraLineListComponent } from '../member-health-infra-line-list/member-health-infra-line-list';
import { UtilityHelperProvider } from '../../providers/utility-helper/utility-helper';

/**
 * Generated class for the Covid19ResultsComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'covid19-results',
  templateUrl: 'covid19-results.html'
})
export class Covid19ResultsComponent {

  covid19Results:any[]=[];
  headingDetails: any;
  standByFacilities: any[] = [];

  constructor(private queryExcuter:QueryExecutorProvider,
    public navCtrl: NavController,
    public utils: UtilityHelperProvider,
    private loading: LoadingController) {
  }
  ngOnInit(){
    let queryDto:any ={
      code: 'soh_covid19_infrastructure_wise_results',
      parameters :{} 
    }
    let loader = this.loading.create();
    loader.present();
    this.queryExcuter.execute(queryDto).subscribe((data)=>{
      this.covid19Results=data.result.filter(x => x.IsCovid19Lab || x.Suspected !== 0);
      this.standByFacilities = data.result.filter(x => !x.IsCovid19Lab && x.Suspected === 0);
      this.headingDetails = this.covid19Results.length > 0  ? this.covid19Results[0] : null;
      if(this.headingDetails){
        this.headingDetails.showOtherDetails = false;
      }
      if (this.covid19Results.length > 0) {
        this.covid19Results.shift();
        this.covid19Results.forEach(element => {
          element.showOtherDetails = false;
        });
      }

      if(this.standByFacilities.length > 0){
        this.standByFacilities.forEach(element => {
          element.showOtherDetails = false;
        });
      }
      
      loader.dismiss();
    },(err)=>{
      loader.dismiss();
      this.utils.onApiFailure(err);
    });
  }

  goToOtherDeatils(result,name,code) {
    this.navCtrl.push(MemberHealthInfraLineListComponent,
      { "queryCode": code,
        "healthInfraId": result.health_infra_id,
        "title": name,
        "name": result.Name});
  }

}



