import { Component } from '@angular/core';
import { LoginServiceProvider } from '../../providers/login-service/login-service';
import { NavController, ToastController, NavParams, Events, LoadingController } from 'ionic-angular';
import { StateOfHealthComponent } from '../state-of-health/state-of-health';
import { LocalStorageProvider } from '../../providers/local-storage/local-storage';
import { LOCAL_STORAGE, LINELIST_TYPE, ROOT_LOCATION } from '../../app/constants';
import { LoginCodeComponent } from '../login-code/login-code';
import { UtilityHelperProvider } from '../../providers/utility-helper/utility-helper';
import { RegisterFormComponent } from '../register-form/register-form';
import { MemberLineListComponent } from '../member-line-list/member-line-list';
import { InMemoryStorageProvider } from '../../providers/in-memory-storage/in-memory-storage';
import { TranslateService } from '@ngx-translate/core';
import { StateOfHealthServiceProvider } from '../../providers/state-of-health-service/state-of-health-service';

/**
 * Generated class for the LoginComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'login',
  templateUrl: 'login.html'
})
export class LoginComponent {

  userName:string;    //istiyak_trainer
  password:string;
  isBackButtonEnable:boolean=false;
  rootLocation = ROOT_LOCATION;
  constructor(private loginService:LoginServiceProvider, 
    private navCtrl: NavController,
    private localStorage:LocalStorageProvider,
    public event:Events,
    private loading:LoadingController,
    private inMemoryStorage:InMemoryStorageProvider,
    private util:UtilityHelperProvider,
    private toast: ToastController,
    private navParams:NavParams,
    private stateOfHealthService: StateOfHealthServiceProvider,
    private translateService:TranslateService) {

      this.isBackButtonEnable = this.navParams.get("isBackButtonEnable") || false;
  }
  onLogin(){
    let loading = this.loading.create();
    loading.present();
    this.loginService.login(this.userName,this.password).subscribe(token=>{
      this.localStorage.set(LOCAL_STORAGE.TOKEN,token);
      this.loginService.getLoginUser().subscribe(user=>{
        this.stateOfHealthService.initializeUseApplicationAccessDetails(user);
        this.util.setRootLocation(user.minLocationId)
        this.localStorage.set(LOCAL_STORAGE.LOGIN_USER,user).then(()=>{
          this.localStorage.set(LOCAL_STORAGE.IS_ANONYMOUS_LOGIN,false);
          const data = this.inMemoryStorage.get(LOCAL_STORAGE.AREA_ELEMENT);
          const dateFilter  = this.inMemoryStorage.get(LOCAL_STORAGE.CORRENT_DATE_FILTER);
          const elementConfig = this.inMemoryStorage.get(LOCAL_STORAGE.ELEMENT_CONFIG);
          this.event.publish("Data quality",true);
          this.event.publish("Change language",user);
          this.stateOfHealthService.getElementConfigJsonPermissionWise(user.id).subscribe(res =>{
            this.stateOfHealthService.setElementConfig(res.body);
            loading.dismiss();
            if (res.status == 200) {
              if (data && (data['level'] == elementConfig['lineList']['lastLevel'])) {
                this.navCtrl.pop();
                this.navCtrl.insert(1, this.util.getLandingComponent(user.minLocationLevel));
                this.navCtrl.push(MemberLineListComponent, { elementName: data['elementName'], locationId: data['locationId'], element: data, selectedDateFilter: dateFilter, selectedElementConfig: elementConfig, type:LINELIST_TYPE.ELEMENT });
                this.inMemoryStorage.remove(LOCAL_STORAGE.AREA_ELEMENT);
                this.inMemoryStorage.remove(LOCAL_STORAGE.ELEMENT_CONFIG);
              } else {
                this.navCtrl.setRoot(this.util.getLandingComponent(user.minLocationLevel));
              }
            }
          },err=>{
            loading.dismiss();
            this.util.showToast(this.translateService.instant("ErrorMsg"));
          })
        })
      },err=>{
        loading.dismiss();
        this.util.showToast(this.translateService.instant("ErrorMsg"));
      })
      
    }, (failed)=>{
      loading.dismiss();
      let message = this.toast.create({message:(failed.error && failed.error.error_description) || this.translateService.instant("InavalidUsernameOrPasswordMessage"),duration:1000});
      message.present();
    })
 
  }
  onLoingWithCode(){
    this.navCtrl.push(LoginCodeComponent);
  }
  onLoginAnonymous(){
    this.localStorage.set(LOCAL_STORAGE.IS_ANONYMOUS_LOGIN,true);
    this.navCtrl.setRoot(StateOfHealthComponent);
  }
  onBack(){
    this.navCtrl.pop();
  }
  onRegisterForm(){
    this.navCtrl.push(RegisterFormComponent);
  }

}



