import { Component, ViewChild, OnInit } from '@angular/core';
import { LoadingController, Content, Events, NavController } from 'ionic-angular';
import { QueryExecutorProvider } from '../../providers/query-executor/query-executor';
import { StateOfHealthServiceProvider } from '../../providers/state-of-health-service/state-of-health-service';
import { TIME_LINE, LINELIST_TYPE, LOCAL_STORAGE, ROOT_LOCATION } from '../../app/constants';
import { StateOfHealthMapComponent } from '../state-of-health-map/state-of-health-map';
import { UtilityHelperProvider } from '../../providers/utility-helper/utility-helper';
import { PieChartComponent } from '../pie-chart/pie-chart';
import { InMemoryStorageProvider } from '../../providers/in-memory-storage/in-memory-storage';
import { LocalStorageProvider } from '../../providers/local-storage/local-storage';
import { StatusReportComponent } from '../status-report/status-report';
import { Covid19ResultsComponent } from '../covid19-results/covid19-results';
import { LineChartComponent } from '../line-chart/line-chart';
import { TranslateService } from '@ngx-translate/core';
import { LoginServiceProvider } from '../../providers/login-service/login-service';

/**
 * Generated class for the StateOfHealthWebComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'state-of-health-web',
  templateUrl: 'state-of-health-web.html'
})
export class StateOfHealthWebComponent implements OnInit {

  stateData: any;
  isDataLoad: boolean = false;
  isDataAvailable: boolean = false;
  isServerDown: boolean = false;
  drillingEnable: boolean = true;
  selectedElement: any = {};
  lineList: any;
  data: any;
  result: any;
  locationLevel: any = 0;
  elementHistory:any=[];
  selectedDateFilter:any;
  dateFilter:any;
  token: boolean ;
  selectedElementHelp:any;
  elementsConfig:any;
  selectedElementConfig:any;
  selectedTabData:any;
  timeLineLabel1:any = `{${TIME_LINE}}`;
  timeLineLabel:any = `{${TIME_LINE}}`;
  chartData:any;
  elements:any;
  lineListType:any;
  field:any;
  lineListLabel:any;
  keys:any;
  dateFilterOptions:any;
  heading1:any;
  heading2:any;
  totalHeading:any;
  user: any;
  randomMapId: any;
  @ViewChild(StateOfHealthMapComponent) map: StateOfHealthMapComponent;
  @ViewChild(PieChartComponent) piechart: PieChartComponent;
  @ViewChild(Content) content: Content;
  constructor(
    private loading: LoadingController,
    private queryExecutor: QueryExecutorProvider,
    private utilityHelper: UtilityHelperProvider,
    public inMemoryStorage: InMemoryStorageProvider,
    private stateOfHealthService: StateOfHealthServiceProvider,
    public localStorage: LocalStorageProvider,
    private utilHelper:UtilityHelperProvider,
    private navCtrl: NavController,
    public event: Events,
    public loginService: LoginServiceProvider,
    private translateService:TranslateService) {

  }
  resetLocation(){
    this.selectedElement.elementName = this.selectedTabData.elementName;
    this.selectedElement.english_name = ROOT_LOCATION.LOCATION_NAME;
    this.selectedElement.locationId = ROOT_LOCATION.LOCATION_ID;
    this.locationLevel = 0;
  }
  ngOnInit() {
    this.localStorage.remove(LOCAL_STORAGE.WEBTOKEN).then(() => {
      this.dateFilterOptions = this.inMemoryStorage.get(LOCAL_STORAGE.TIME_LINE_INTERVALS);
      this.selectedDateFilter = this.selectedDateFilter = this.inMemoryStorage.get(LOCAL_STORAGE.CORRENT_DATE_FILTER);
      this.dateFilter = this.selectedDateFilter.value;
      let making = this.loading.create();
      making.present();
      this.setWebToken().then(res => {
        if (res) {
          this.loginService.getLoginUser().subscribe(user => {
            this.user = user;
            if (user) {
              this.stateOfHealthService.getElementConfigJsonPermissionWise(user.id).subscribe(res => {
                this.stateOfHealthService.setElementConfig(res.body);
                this.initializeWeb();
                console.info('map' + this.map);
                this.init(ROOT_LOCATION.LOCATION_ID);
                this.token = true;
                making.dismiss();
              }, err => {
                making.dismiss();
                this.token = false;
              });
            } else {
              making.dismiss();
              this.token = false;
            }
          }, err => {
            making.dismiss();
            this.token = false;
          })
        } else {
          making.dismiss();
          this.token = false;
        }
      }).catch(err => {
        making.dismiss();
        this.token = false;
      })
    }).catch(()=>{
      this.token = false;
    })
    
  }
  private initializeWeb(){
    this.elementsConfig = this.stateOfHealthService.getElementConfig();
    // set default element
    let selectedElementConfig = this.elementsConfig.find(x => x.module === 'RCH');
    let selectedTabData = selectedElementConfig.elements.find(x => x.elementName === 'Anemia');
    this.selectedElementConfig = selectedTabData;
    this.selectedTabData = selectedTabData.tabs[0];
    this.totalHeading = this.selectedTabData.noOfHeading || 1;
    this.timeLineLabel = this.selectedTabData.elementDisplayNamePostfix.replace(`{${TIME_LINE}}`, this.selectedDateFilter.displayValue);
    this.heading1 = this.selectedTabData.headingLabel1 ? this.selectedTabData.headingLabel1.replace(`{${TIME_LINE}}`, this.selectedDateFilter.displayValue) : this.selectedTabData.headingLabel1;
    this.heading2 = this.selectedTabData.headingLabel2 ? this.selectedTabData.headingLabel2.replace(`{${TIME_LINE}}`, this.selectedDateFilter.displayValue) : this.selectedTabData.headingLabel2;
    this.resetLocation();
  }
  setWebToken(){
    return new Promise((resolve,reject)=>{
      this.localStorage.get(LOCAL_STORAGE.WEBTOKEN).then(token=>{
        if(token){
          this.inMemoryStorage.put(LOCAL_STORAGE.WEBTOKEN,token);
          resolve(true);   
        }else{ 
          if (document.URL.indexOf("?") > 0) {
            let splitURL = document.URL.split("?");
            if(splitURL[1].indexOf("&")>0){
              let splitParams = splitURL[1].split("&");
              if(splitParams[0].indexOf('=')>0 && splitParams[1].indexOf('=')>0){
                let singleURLParam = splitParams[0].split('=');
                let singleURLParam1 = splitParams[1].split('=');
                if(singleURLParam[0]=='token' && singleURLParam1[0]=="rtoken"){
                  this.localStorage.set(LOCAL_STORAGE.WEBTOKEN,{'access_token':singleURLParam[1],'refresh_token':singleURLParam1[1],'scope':"write",'token_type':"bearer"});
                  this.inMemoryStorage.put(LOCAL_STORAGE.WEBTOKEN,{'access_token':singleURLParam[1],'refresh_token':singleURLParam1[1],'scope':"write",'token_type':"bearer"});
                  this.token=true;
                  resolve(true)
                } else{
                  this.token=false;
                  reject(false);
                } 
              } else{
                this.token=false;
                reject(false);
              } 
            } else{
              this.token=false;
              reject(false);
            } 
          }else {
            this.token=false;
            reject(false);
          }
        }
      });
    })
  }
  init(locationId) {
    let making = this.loading.create();
    making.present();
    this.queryExecutor.execute({ "code": "get_elements_details_permission_wise_3", "parameters": { "locationId": locationId || ROOT_LOCATION.LOCATION_ID, 'timeline_type' : this.selectedDateFilter.value, 'user_id': this.user.id } }
    ).subscribe((data) => {
      this.result = data.result;
      this.data = this.stateOfHealthService.prepareData(data.result);
      this.elements = this.data;
      this.onSelectLocation(this.data[this.selectedElement.elementName],false);
      this.isDataAvailable = Object.keys(data.result).length > 0;
      this.isDataLoad = true;
      making.dismiss();
    }, (error) => {
      this.isDataLoad = true;
      if (error.status == 0) {
        this.isServerDown = true;
      } else {
        this.isDataAvailable = false;
      }
      making.dismiss();
    })
  }
  public getFile(elementConfig){
    return this.stateOfHealthService.getFile(elementConfig);  
  }
  setTabData(data){
    this.selectedTabData = data;
    this.totalHeading = this.selectedTabData.noOfHeading || 1;
    this.timeLineLabel = this.selectedTabData.elementDisplayNamePostfix.replace(`{${TIME_LINE}}`, this.selectedDateFilter.displayValue);
    this.heading1 = this.selectedTabData.headingLabel1 ? this.selectedTabData.headingLabel1.replace(`{${TIME_LINE}}`, this.selectedDateFilter.displayValue) : this.selectedTabData.headingLabel1;
    this.heading2 = this.selectedTabData.headingLabel2 ? this.selectedTabData.headingLabel2.replace(`{${TIME_LINE}}`, this.selectedDateFilter.displayValue) : this.selectedTabData.headingLabel2;
    this.lineList=null;
    this.selectedElement = this.elements[this.selectedTabData.elementName];
    this.loadMapAndPieChart();  
  }
  onSelectElement(elementConfig) {
    if (elementConfig.elementName === 'COVID_STATUS_REPORT') {
      this.navCtrl.push(StatusReportComponent);
      return;
    } else if (elementConfig.elementName === 'COVID19_HEALTH_INFRA_REPORT') {
      this.navCtrl.push(Covid19ResultsComponent);
      return;
    }
    this.lineList=null;
    this.locationLevel = 0;
    this.selectedElementConfig = elementConfig;
    this.selectedTabData = elementConfig.tabs[0];
    this.totalHeading = this.selectedTabData.noOfHeading || 1;
    this.timeLineLabel = this.selectedTabData.elementDisplayNamePostfix.replace(`{${TIME_LINE}}`, this.selectedDateFilter.displayValue);
    this.heading1 = this.selectedTabData.headingLabel1 ? this.selectedTabData.headingLabel1.replace(`{${TIME_LINE}}`, this.selectedDateFilter.displayValue) : this.selectedTabData.headingLabel1;
    this.heading2 = this.selectedTabData.headingLabel2 ? this.selectedTabData.headingLabel2.replace(`{${TIME_LINE}}`, this.selectedDateFilter.displayValue) : this.selectedTabData.headingLabel2;
    this.elements = this.data;
    this.selectedElement = this.elements[elementConfig.tabs[0].elementName];
    this.elementHistory = [];
    this.elementHistory.push(this.data[elementConfig.tabs[0].elementName]);
    this.loadData(this.selectedElement,true);
    this.content.scrollToTop();
  }
  loadMapAndPieChart(){
    //For svg support
    if (this.selectedElement.level >= this.selectedTabData.minLocationLevel && this.selectedElement.level <= this.selectedTabData.maxLocationLevel) {
      let tag = document.getElementById(`footerDescription${this.selectedElement.level}`);
      if (tag) {
        tag.innerHTML = this.selectedTabData.footerDescription || null;
      }
    }
    this.stateData = this.chartData
    if (this.map!=null) {
      this.map.reloadData(this.stateData, this.selectedElement, this.locationLevel,this.selectedTabData);
    }
    if(this.piechart!=null  && this.selectedTabData.pieChart.isEnable){
      this.piechart.reloadData(this.selectedElement,this.selectedTabData);
    }
  }
  onSelectLocation(element, isLoadMap = true) {
    let n = this.utilityHelper.isClickable(element,this.selectedTabData,this.drillingEnable);
    if (n==1 || n==3) {
      return;
    } else if (n==2) {
      this.lineListType = LINELIST_TYPE.ELEMENT;
      this.showLineList(element);
    } else {
      this.lineList=null;
      this.selectedElement = element;
      this.setElements();
      this.elementHistory.push(element);
    if(isLoadMap){
      this.locationLevel = this.locationLevel + 1;
    }
    this.loadData(element, isLoadMap);
    this.content.scrollToTop();
  }
  this.event.subscribe("CMDashBoardLineList",(field,element)=>{
    this.field = field;
    this.lineListType = LINELIST_TYPE.FIELD;
    this.showLineList(element);
  });
  }
  setElements(){
    let making = this.loading.create();
    making.present();
    this.queryExecutor.execute({ "code": "get_elements_details_permission_wise_3", "parameters": { "locationId": this.selectedElement.locationId || ROOT_LOCATION.LOCATION_ID, 'timeline_type' : this.selectedDateFilter.value, 'user_id': this.user.id }}
    ).subscribe((data) => {
      this.elements = this.stateOfHealthService.prepareData(data.result);    
      making.dismiss();
    }, (error) => {
      making.dismiss();
    })
  }
  onBack(){  
    if(this.elementHistory.length == 1){
      this.onSelectElement(this.selectedElement)
    }else{
      this.elementHistory.pop();    // remove  it self
      let element = this.elementHistory.pop();  
      this.locationLevel = this.locationLevel-2;  // remove it + previous
      this.onSelectLocation(element);
    }
    this.lineList=null;
  }
  public isDisplayFlag() {
    if (this.selectedElement.type == 'S' || this.selectedElement.type == 'D' || this.selectedElement.type == 'C' || this.selectedElement.type == 'B' || this.selectedElement.type == 'Z') {
      if(this.selectedElementConfig.enableReporting)
      {
        return true;
      }else{
        return false;
      }
    } else {
      return false;
    }
  }  
  showLineList(element) {
      let queryCode;
      if(this.lineListType == LINELIST_TYPE.ELEMENT){
        queryCode = this.selectedTabData.lineList.query;
        this.lineListLabel = this.selectedTabData.lineList.elementLineListLable;
      }else if(this.lineListType == LINELIST_TYPE.FIELD){
        queryCode = this.field.query;
        this.lineListLabel = this.field.name;
      }
      let betweenDates;
      if(this.selectedTabData.elementName == "FI"){
         betweenDates = this.utilHelper.getBetweenDays(this.selectedDateFilter.value);
      }else{
         betweenDates = this.utilHelper.getBetweenDates(this.selectedDateFilter.value);
      }
      let params = {
        "code": queryCode,
        "parameters": { "location_id": element.locationId, limit: 10000, fromDate: betweenDates[0],toDate: betweenDates[1] }
      };
    let making = this.loading.create();
    making.present();
    this.queryExecutor.executeQuery(params).subscribe(response => {
      this.lineList = response.result;
      if(this.lineList.length>0){
        this.keys = Object.keys(this.lineList[0]);
        }
      making.dismiss();
    }, () => {
      making.dismiss();
    })
 }
  loadData(selectedElement, isReloadMap) {
    let loading = this.loading.create();
    loading.present();
    this.queryExecutor.execute({ "code": "state_of_health_data_retrieval_15", "parameters": { "locationId": selectedElement.locationId, 'timeline_type' :  this.selectedDateFilter.value, 'element_name': this.selectedTabData.elementName } }).subscribe(response => {
      this.chartData = response.result;
      this.randomMapId = "Map-" + Math.floor(Math.random() * 100) + 1;
      this.loadMapAndPieChart();
      loading.dismiss();
    }, (error) => {
      loading.dismiss();
      this.utilityHelper.onApiFailure(error);
    })
  }
  onShowMore(member,element){
    this.utilHelper.onShowMoreForMemberList(member,element,this.selectedTabData);
  }
  public onDateFilterChange(newvalue){
    this.selectedDateFilter = this.dateFilterOptions.find(interval => interval.value === this.dateFilter);
    this.timeLineLabel=this.selectedTabData.elementDisplayNamePostfix.replace(`{${TIME_LINE}}`,this.selectedDateFilter.displayValue);
    this.data = this.stateOfHealthService.prepareData(this.result);
    this.resetLocation();
    this.lineList=null;
    this.locationLevel = 0;
    this.elementHistory = [];
    this.init(2);
  }
onShowDetails(element){
  if(element.isShow=="undefined"){
    element.isShow=true;
  }else{
    element.isShow=!element.isShow;
  }
}
isClickable(element){
  let n = this.utilityHelper.isClickable(element,this.selectedTabData,this.drillingEnable);
  if(n==1 || n==3){
    return false;
  }else{
    return true;
  }
}
getVaccinationName(vaccination){
  return this.utilHelper.getVaccinationName(vaccination);  
}
getVaccinationChangeName(vaccination){
  return this.utilHelper.getVaccinationChangeName(vaccination);
}

showLineChart(){
  this.navCtrl.push(LineChartComponent,{
    selectedElementConfig: this.selectedTabData,
    selectedElement: this.selectedElement,
    elementConfig: this.selectedElementConfig
  });
}
}



