import { Pipe, PipeTransform } from '@angular/core';

/**
 * Generated class for the SortPipe pipe.
 *
 * See https://angular.io/api/core/Pipe for more info on Angular Pipes.
 */
@Pipe({
    name: 'sort',
    pure : false
})
export class SortPipe implements PipeTransform {
    /**
     * Takes a value and makes it lowercase.
     */
    transform(values: Array<any>, sortField: string = 'valueDisp', order: string = 'ASC') {
        let secondField = 'sortPriority';
        let me = this;
        values.sort(function (a, b) {
            if(a[secondField] == b[secondField]){
                return me.getOrder(a,b,order,sortField);
            }else if (a[secondField] < b[secondField]) {
                return -1;
            } else if (a[secondField] > b[secondField]) {
                return 1;
            } else {
                return me.getOrder(a,b,order,sortField);
            }
        });
        return values;
    }
    public getOrder(a,b,order,sortField){
        if (order == 'DESC') {
            if (a[sortField] < b[sortField]) {
                return -1;
            } else if (a[sortField] > b[sortField]) {
                return 1;
            } else {
                return 0;
            }
        } else {
            if (a[sortField] > b[sortField]) {
                return -1;
            } else if (a[sortField] < b[sortField]) {
                return 1;
            } else {
                return 0;
            }
        }
    }
}



