import { Component } from '@angular/core';
import { Platform } from 'ionic-angular';
import { QueryExecutorProvider } from '../../providers/query-executor/query-executor';
import { APP, API_HOST, ROOT_LOCATION } from '../../app/constants'
/**
 * Generated class for the AboutUsComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'about-us',
  templateUrl: 'about-us.html'
})
export class AboutUsComponent {

  rootLocation = ROOT_LOCATION;
  text: string;
  appCurrentVersion:any;
  buildDate: any;
  apiHost = API_HOST
  constructor(public platform: Platform,private queryExecutor:QueryExecutorProvider) {
    this.setAppVersion();
    this.setBuildDate();
  }
  setAppVersion(){
    if (this.platform.is('android')) {
      this.appCurrentVersion = APP.ANDROID_DISPLAY_VERSION;
    } else {
      this.appCurrentVersion = APP.IOS_DISPLAY_VERSION;
    }
  }
  setBuildDate(){
    this.queryExecutor.readFile('assets/build.txt' ).subscribe(data=>{
      this.buildDate = data.buildDate;
    });
  }
}



