import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { API_HOST, APP } from '../../app/constants';
import { Observable } from 'rxjs';
import { QueryExecutorProvider } from '../query-executor/query-executor';
import { LoginServiceProvider } from '../login-service/login-service';
import { Platform } from 'ionic-angular';
import { AndroidPermissions } from '@ionic-native/android-permissions';
import { Uid } from '@ionic-native/uid';

/*
  Generated class for the StateOfHealthServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class StateOfHealthServiceProvider {
  elementsConfigs:any;
  
  constructor(public http: HttpClient,
    private queryExecutor: QueryExecutorProvider,
    private loginService:LoginServiceProvider,
    private platform: Platform,
    private androidPermissions: AndroidPermissions,
    private uid: Uid
    ) {
    console.log('Hello StateOfHealthServiceProvider Provider');
  }

  public getElementConfigJsonPermissionWise(userId):Observable<any>{
    if(userId){
      return this.http.get(API_HOST+'/api/mobile/soh/elementsJson?userId='+userId,{observe:'response'});
    }else {
      return this.http.get(API_HOST+'/api/mobile/soh/elementsJson/',{observe:'response'});
    }
  }
  
  public getFile(elementConfig){
    return API_HOST+'/api/mobile/getfileById?id='+ elementConfig.fileId;
  }
  public prepareData(elements) {
    let data = {};
    for (let element of elements) {
      var name = element.elementName;
      data[name] = element;    
    }
    return data;
  }


  public getRemainingVaccination(member):Observable<any>{
    let dob=member.dob.split('-');
    dob=new Date(dob[2],dob[1],dob[0]);
    return this.http.get(API_HOST+"/api/mobile/getimmunisationsforchild",{observe: 'response', params: { "dateOfBirth" : dob.getTime().toString() , "givenImmunisations" : member.immunisationGiven} });
  }
  public setElementConfig(elementConfig:any){
      this.elementsConfigs = this.sortElementConfig(elementConfig);
  }
  public getElementConfig(){
    return this.elementsConfigs;
  }
  sortElementConfig(elementConfig){
      for(let module of elementConfig){
          module.elements.sort(compare);
      }
    function compare(a, b) {
        const orderA = Number(a.order);
        const orderB = Number(b.order);
      
        let comparison = 0;
        if (orderA > orderB) {
          comparison = 1;
        } else if (orderA < orderB) {
          comparison = -1;
        }
        return comparison;
      }
      return elementConfig;
  }

  initializeUseApplicationAccessDetails(user) {
    let param = {
      userId: user.id,
      appName: 'SOH',
      appVersion: this.platform.is('android') ? APP.ANDROID_DISPLAY_VERSION : APP.IOS_DISPLAY_VERSION,
      deviceType: this.platform.is('android') ? 'ANDROID' : 'IOS',
      imei: ''
    }
    
    this.getImei().then(imei => {
      param.imei = imei;
      this.insertUserAccessDetails(param);
    }, err => {
      param.imei = '';
      this.insertUserAccessDetails(param);
    });
  }

  insertUserAccessDetails(param){
    let query = {
      code: 'insert_um_user_app_access_details_1',
      parameters: param
    }
    this.queryExecutor.execute(query).subscribe(res => {
    }, err => {
    })
  }

  async getImei() {
    const { hasPermission } = await this.androidPermissions.checkPermission(
      this.androidPermissions.PERMISSION.READ_PHONE_STATE
    );

    if (!hasPermission) {
      const result = await this.androidPermissions.requestPermission(
        this.androidPermissions.PERMISSION.READ_PHONE_STATE
      );

      if (!result.hasPermission) {
        throw new Error('Permissions required');
      }

      // ok, a user gave us permission, we can get him identifiers after restart app
      return this.uid.IMEI;
    }

    return this.uid.IMEI
  }
  
}



